<script>
import apiUtils from "../utils/ApiUtil.vue";
export default {
  data() {
    return {
      originFontSize: document.documentElement.style.fontSize,
      showPay: false,
      mount: 0,
      price: 0,
      account: "",
      isModalVisible: false,
    };
  },

  methods: {
    back() {
      this.$router.back();
    },
    pay(price) {
      //检查是否存在待支付订单 ，如果有就返回来。
      apiUtils.checkOrder().then((data) => {
        //存在支付订单
        if (data.hasNoPayOder == 1) {
          this.showModal();
        } else {
          this.showPayType(price);
        }
      });
    },
    showPayType(price) {
      apiUtils.getOrderInfo().then((data) => {
        this.showPay = true;
        if (data.price == null) {
          this.price = price;
          this.mount = this.getMountByPrice(price);
        } else {
          this.price = data.price;
          this.mount = data.mount;
        }
        this.account = data.account;
      });
    },
    getMountByPrice(price) {
      if (price == 10) {
        return 100;
      } else if (price == 20) {
        return 300;
      } else if (price == 50) {
        return 800;
      } else if (price == 100) {
        return 1800;
      } else {
        return 0;
      }
    },
    closePay() {
      this.showPay = false;
    },
    payment(type) {
      this.closePay();
      //调用后台支付订单生成接口，并跳转到指定的支付页面
      apiUtils.createOrder({ money: this.price, payType: type }).then((res) => {
        window.location.href = res;
      });
    },
    showModal() {
      this.reset();
      this.isModalVisible = true;
    },
    onModalHide() {
      console.log("我已支付");
      this.set();
      this.isModalVisible = false;
    },
    onModalOk() {
      //从服务器获取支付订单支付页面
      apiUtils.getPayOrderUrl().then((res) => {
        window.location.href = res;
      });
    },
    set() {
      document.documentElement.style.fontSize = "13.33333333vw";
    },
    reset() {
      if (!this.originFontSize) {
        this.originFontSize = "20px";
      }
      document.documentElement.style.fontSize = this.originFontSize;
    },
  },
  mounted() {
    this.set();
    this.$setMetaData("充值");
    //检查是否存在待支付订单 ，如果有就返回来。
    apiUtils.checkOrder().then((data) => {
      //存在支付订单
      if (data.hasNoPayOder == 1) {
        this.showModal();
      }
    });
  },
  unmounted() {
    this.reset();
  },
};
</script>

