<!-- 漫画列表页 -->
<template>
  <!-- loading -->
  <LoadingCmp ref="loading" />
  <!-- 顶部 -->
  <TopCmp :topTitle="categoryName" />
  <!-- Tab页 -->
  <section class="categoryList-tab under-top-bar">
    <div class="tab-list">
      <a
        id="tab_update"
        v-bind:class="{ 'tab-list-item': true, update: true, active: tab == 1 }"
        @click="tabClick(1)"
        >漫画列表</a
      >
    </div>
  </section>
  <!-- 数据列表 -->
  <section class="categoryList-content content-hot">
    <div id="list_hot_box" class="comic-list hot active">
      <ul id="list_hot">
        <li class="comic-item" v-for="(comic, index) in comicList" :key="index">
          <router-link :to="'/detail/' + comic.id" class="comic-link">
            <div class="comic-cover">
              <img class="cover-image" :src="comic.coverImage" />
            </div>
            <div class="comic-info">
              <strong class="comic-title">{{ comic.title }}</strong>
              <small class="comic-update">{{ comic.update }}</small>
              <small class="comic-hot">人气：{{ comic.hot }}</small>
              <small class="comic-fav">收藏数：{{ comic.fav }}</small>
              <small class="comic-tag">{{ comic.tag }}</small>
              <small class="comic-desc">{{ comic.desc }}</small>
            </div>
          </router-link>
        </li>
      </ul>
    </div>
  </section>
  <!-- 提示组件 -->
  <TipCmp ref="tip" @loadMore="handleScroll" :cmp="'comicList'" />
  <!-- 返回顶部 -->
  <BackToTopCmp />
</template>
<script>
import TopCmp from "../common/TopCmp.vue";
import TipCmp from "../common/ListTipCmp.vue";
import BackToTopCmp from "../common/BackToTopCmp.vue";

import apiUtils from "../utils/ApiUtil.vue";
import LoadingCmpVue from "../common/LoadingCmp.vue";
import { decryptImageUrl } from "../utils/AES.vue";
export default {
  //分类名字 和分类id
  data() {
    return {
      categoryName: this.$route.params.categoryName,
      categoryId: this.$route.params.categoryId,
      tab: 1, // 1 更新 2 人气 3 收藏 。默认更新,
      comicList: [],
    };
  },
  components: {
    TopCmp,
    TipCmp,
    BackToTopCmp,
    LoadingCmp: LoadingCmpVue,
  },
  methods: {
    //切换tab
    tabClick(t) {
      if (this.tab == t) {
        return;
      }
      this.tab = t;
      this.comicList = [];
      this.$refs.tip.reset();
      this.getComicData(1);
    },

    async getComicData(page = 1, pageSize = 6) {
      //已经达到最大页了
      if (!this.$refs.tip.canScroll()) {
        return;
      }
      //loading
      this.$refs.loading.show();
      //设置动效
      this.$refs.tip.loadingStart();

      try {
        const response = await apiUtils.getCommicList({
          page: page,
          pageSize: pageSize,
          id: this.categoryId,
          orderBy:
            this.tab == 1
              ? "purchases"
              : this.tab == 2
              ? "favorites"
              : "favorites",
        });

        //解密图片地址
        await Promise.all(
          response.dataList.map(async (comic) => {
            comic.coverImage = await decryptImageUrl(comic.coverImage);
          })
        );

        this.comicList = this.comicList.concat(response.dataList);

        this.currentPage = page;
        const total = response.total;
        const maxPage = Math.ceil(total / pageSize);
        //是否最大页
        this.$refs.tip.loadingEnd(
          this.currentPage == Math.max(maxPage, 1),
          this.comicList.length > 0
        );
        //隐藏loading
        this.$refs.loading.hide();
      } catch (err) {
        this.$refs.tip.hasError();
      }
    },
    handleScroll() {
      this.getComicData(this.currentPage + 1);
    },
  },
  mounted() {
    this.$setMetaData("漫画列表");
    this.getComicData(1);
    // window.addEventListener("scroll", this.handleScroll);
  },
  unmounted() {
    // window.removeEventListener("scroll", this.handleScroll);
  },
};
</script>
