<!-- 搜索结果的列表页 -->
<template>
  <!-- loading -->
  <LoadingCmp ref="loading" />
  <section class="result-box under-top-bar">
    <ul class="result-list" v-if="hasData">
      <li class="comic-item" v-for="(comic, index) in comicList" :key="index">
        <router-link :to="'/detail/' + comic.id" class="comic-link">
          <div class="comic-cover">
            <img :src="comic.cover" class="cover-image" />
          </div>
          <div class="comic-content">
            <strong class="comic-title">{{ comic.title }}</strong>
            <small class="comic-update">更新到 {{ comic.update }}话</small>
            <small class="comic-tag">{{ comic.tag }}</small>
            <small class="comic-desc">{{ comic.desc }}</small>
          </div>
        </router-link>
      </li>
    </ul>
    
  </section>
 <!-- 提示组件 -->
  <TipCmp ref="tip" @loadMore="handleScroll" />
  
</template>

<script>
import TipCmp from "../common/ListTipCmp.vue";
import LoadingCmp from "../common/LoadingCmp.vue";
import { decryptImageUrl } from "../utils/AES.vue";
import apiUtils from "../utils/ApiUtil.vue";
export default {
  props: ["queryKey"],
  data() {
    return {
      comicList: [],
      loading: false,
      maxpage: false,
      err: false,
      hasData: false,
    };
  },
  components: { LoadingCmp ,TipCmp},
  methods: {
    async getComicData(page = 1, pageSize = 6) {
      //已经达到最大页了
      if (this.maxpage || this.loading) {
        return;
      }
      //设置动效
      this.$refs.loading.show();
      this.loading = true;
      this.err = false;
  // //设置动效
      this.$refs.tip.loadingStart();
      try {
        const data = await apiUtils.query({
          p: page,
          s: pageSize,
          //去掉queryKey中的"查询："
          k: this.queryKey.substring(3),
        });

        //解密图片地址
        await Promise.all(
          data.dataList.map(async (item) => {
            item.cover = await decryptImageUrl(item.cover);
          })
        );

        this.comicList = this.comicList.concat(data.dataList);
        this.currentPage = page;
        var maxPage = Math.max(1, Math.ceil(data.total / pageSize));
        //是否最大页
        this.maxpage = this.currentPage == maxPage;
        this.hasData = !(data.dataList.length == 0);
        //是否最大页
        this.$refs.tip.loadingEnd(
          this.currentPage == maxPage,
         this.hasData
        );
        this.$refs.loading.hide();
        this.loading = false;
      } catch (error) {
        this.err = true;
      }
    },
    handleScroll() {
      this.getComicData(this.currentPage + 1);
    },
  },
  mounted() {
    this.getComicData(1);
    //window.addEventListener("scroll", this.handleScroll);
  },
  unmounted() {
   // window.removeEventListener("scroll", this.handleScroll);
  },
};
</script>

