<!-- 漫画阅读页面 -->
<template>
  <!-- 头部悬浮栏区域 hidden -->
  <section class="float-bar top" :class="{ hidden: hidden }">
    <a class="float-bar-btn back" @click="back">[返回]</a>
    <label class="float-bar-title">
      <span class="title">{{ title }}}</span>
      <span class="progress">第{{ seq }}篇 {{ seqTitle }}</span>
    </label>
    <router-link :to="'/detail/' + id" class="float-bar-btn detail">
      <i class="icon icon-detail"></i>
    </router-link>
    <router-link to="/" class="float-bar-btn home">
      <i class="icon icon-home"></i>
    </router-link>
  </section>
  <!-- 底部 -->
  <section class="float-bar bottom" :class="{ hidden: hidden }">
    <a class="float-bar-btn chapter-list" @click="showlChapterList">[目录]</a>
    <a class="float-bar-btn prev-chapter" v-if="seq > 1" @click="gotoNext(-1)"
      >[上一章]</a
    >
    <a
      class="float-bar-btn next-chapter"
      v-if="total > seq"
      @click="gotoNext(1)"
      >[下一章]</a
    >
  </section>

  <!-- 悬浮在右侧的作品目录信息 show animate -->
  <section class="float-chapter-list" :class="{ show: show, animate: animate }">
    <div class="fixed-chapter-list-mask" @click="showlChapterList"></div>
    <div class="chapter-list-rank">
      <h2 class="comic-title">
        {{ title }}
        <span style="font-size: 12px"
          >({{ status == 1 ? "连载中" : "已完结" }})</span
        >
      </h2>
      <div class="chapter-list-desc">
        共{{ total }}话 (可试读至{{ tryNumber }}话)
        <span class="max-free-hint"></span>
        <span
          class="list-order"
          :class="{ asc: chapterOrderAsc, desc: !chapterOrderAsc }"
        >
          <i class="icon-order"></i>
          <span @click="sort">{{ chapterOrderAsc ? "正序" : "倒叙" }}</span>
        </span>
      </div>
      <div class="chapter-list-wrap">
        <ul class="list-wrap normal active" ref="scroll">
          <span v-if="busy">加载中请稍后</span>
          <li
            v-for="(chapter, index) in chapters"
            :key="index"
            class="chapter-item"
          >
            <a
              class="chapter-link"
              :class="{
                lock: chapter.lock == 1,
                unLock: chapter.lock == 2,
                now: chapter.seq == seq,
              }"
              @click="go2Chapter(chapter)"
            >
              <p class="chapter-title">
                {{ chapter.seq }} - {{ chapter.title }}
              </p>
            </a>
          </li>
        </ul>
      </div>
      <div
        class="icon-locate icon-back-to-top to-top"
        id="top_or_bottom"
        @click="goTop"
      ></div>
      <div class="chapter-list-count">
        <span class="count">共{{ total }}话</span>
      </div>
    </div>
  </section>

  <!-- 漫画图片区域 -->
  <section
    class="comic-pic-area"
    style="height: 100vh; overflow: auto; background-color: black"
    @scroll="handleScroll"
    ref="scrollContainer"
  >
    <!-- 浏览控制按键 - 上一页 -->
    <a
      class="nav-ctrl-btn prev"
      v-if="seq > 1 && !isLoading"
      @click="gotoNext(-1)"
      >点击加载上一章节</a
    >
    <a class="nav-ctrl-btn prev" v-if="seq <= 1 && !isLoading"
      >前面没有更多了</a
    >
    <!-- 章节图片列表loading -->
    <div
      class="loading bounce comic-pic-list-loading"
      :style="{ display: loading }"
    >
      <div class="item"></div>
      <div class="item"></div>
      <div class="item"></div>
    </div>
    <!-- 画面内容 -->
    <section class="comic-pic-list-all" ref="list" @click="clickComic">
      <loading-spinner v-if="isLoading" />
      <ul class="comic-pic-list" v-for="seq in seqs" :key="seq.index">
        <ComicPicItem
          v-for="pic in seq.chapters"
          :key="pic.index"
          :index="pic.index"
          :data-timeout="20000"
          :image-url="pic.url"
          :seq="seq.seq"
          :total="seq.chapters.length"
          :scroll-to-me="pic.index === currentIndex"
          :currentIndex="currentIndex"
          @loaded="loadComplete"
        />
      </ul>
    </section>

    <!-- 浏览控制按键 - 下一页 -->
    <a class="nav-ctrl-btn next" v-if="seq >= total && !isLoading"
      >已经没有了
    </a>
    <a
      class="nav-ctrl-btn next"
      v-if="seq < total && !isLoading"
      @click="gotoNext(1)"
      >点击加载下一章节</a
    >

    <!-- 浏览模式提示 -->
    <div class="view-mode-hint">123213</div>
    <!-- 进度框 -->
    <div class="progress-box" :class="{ out: !hidden }">
      {{ seqTitle }} {{ Math.min(Math.max(currentIndex, 1), totalCount) }}/{{
        totalCount
      }}
    </div>
  </section>
  <!-- 提示框 -->
  <modal-confirm
    v-if="isModalVisible"
    :message="message"
    cancel-text="再想想"
    :ok-text="okMsg"
    @hide="onModalHide"
    @ok="onModalOk"
  ></modal-confirm>
  <!-- 提示 -->
  <div
    class="pile-dialog-wrap toast"
    :class="{ open: showScrollTips }"
    style="z-index: 1000010"
  >
    <div
      class="dialog-box"
      :class="{ show: showScrollTips }"
      style="z-index: 1000012"
    >
      <div class="dialog-content">
        <p class="dialog-para">{{ smallTip }}</p>
      </div>
    </div>
  </div>

  <div
    class="modal-blocker-wrap"
    :class="{ show: needBuy }"
    id="modal_block61677139897289"
  >
    <div class="modal-blocker-mask show"></div>
    <div class="modal-blocker-container show">
      <div class="blocker-content">
        <div class="content">购买：第{{ seq }}话 {{ seqTitle }}</div>
        <div class="content">
          <p style="font-size: 0.7rem">
            价格：<span style="color: #ff613e">{{ price }}</span>
            <span style="color: #999">点券</span
            ><i style="display: inline-block; width: 24px"></i>余额：{{
              balance
            }}
            <span style="color: #999">点券</span>
            <small class="weak small" v-if="price > balance">(点券不足)</small>
          </p>
        </div>
      </div>
      <div class="blocker-btn-group">

          

        <router-link to="/pay" class="btn btn-ok" v-if="price > balance && login"
          >去充值点券</router-link
        >
        <a class="btn btn-ok" v-if="price <= balance && login" @click="buythis"
          >点击购买</a
        >

        <a class="btn btn-ok" v-if="!login" @click="toLogin">点击登陆</a>
        <!-- <a href="https://www.baidu.com" class="btn btn-ok">观看广告解锁</a> -->
        <router-link :to="'/detail/' + this.id" class="btn btn-cancel"
          >暂不充值，返回</router-link
        >
      </div>
    </div>
  </div>
</template>
<script>
import apiUtils from "../utils/ApiUtil.vue";
import ComicPicItem from "./ComicImgCmp.vue";
import history from "../utils/History.vue";
import LoadingSpinner from "../common/LoadingCmp2.vue";
export default {
  data() {
    return {
      id: this.$route.params.id,
      seq: this.$route.params.seq, //这是第几章
      isModalVisible: false, //显示模态提示框
      message: "登陆后可以使用购买服务，是否前往登陆页？",
      okMsg: "立即登陆",
      chapters: [],
      chapterOrderAsc: true,
      page: 0, //目录分页
      busy: false, //是否加载目录
      max: false, //目录最大页
      err: false, //目录加载错误
      show: false,
      animate: false,
      hidden: false, //菜单显示
      showScrollTips: false,
      smallTip: "", //小提示
      loading: "block", //加载提示,
      seqs: [], //漫画内容信息
      currentIndex: 0, //当前章读到第几页了 从0开始,
      totalCount: 0, //当前章总页数量,
      buyList: [], //购买列表 ,用于判断是否购买过
      firstOpenMenu: true, //第一次打开菜单
      isLoading: false,
      loadNumerb: 0,
      login: apiUtils.checkLogin()
    };
  },
  components: {
    ComicPicItem,
    LoadingSpinner,
  },

  async mounted() {
    //加载漫画信息
    await this.loadComic();
    // console.log("loading", this.loading);
    //加载漫画内容
    await this.loadComicContent(true);

    //加载历史
    await this.loadHistory();
    //跳转到index页面
    //绑定滚动事件
    this.bindScrollEvent();
    if (!this.needBuy && this.currentIndex > 0) {
      //滑动到已经看到的页面
      this.$nextTick(() => {
        this.goToCurrent();
      });
    }
    //设置seo
    this.$setMetaData(this.title);

    if (this.loading === "none") {
      //下一帧加载
      this.$nextTick(() => {
        //插入或者更新历史记录 { comicId: '456', chapter: '3', page: '2',comicTitle:'xxx',updateChapter:'xxx',icon:'xxx',author:'xxx' }
        history.insertOrUpdate({
          comicId: this.id,
          chapter: this.seq,
          page: 1,
          comicTitle: this.title,
          updateChapter: this.total,
          icon: this.icon,
          author: this.authorName,
        });
      });
    } else {
      console.log("不存在");
    }
  },
  beforeUnmount() {
    //目录滚动事件
    this.$refs.scroll.removeEventListener("scroll", this.loadMoreChapter);
  },
  methods: {
    //滑动查看漫画
    handleScroll() {
      const scrollEl = this.$refs.scrollContainer;
      const idx = this.getTopItemIndex(scrollEl);
      var index = -1;
      var item = scrollEl.getElementsByClassName("comic-pic-item")[idx];
      //是否到底
      if (scrollEl.scrollTop + scrollEl.clientHeight >= scrollEl.scrollHeight) {
        index = this.totalCount;
      } else if (item) {
        index = item.getAttribute("index");
      }
      if (this.currentIndex != index) {
        this.currentIndex = index;
        //如果comicPicItem组件存在
        if (item) {
          var seq = item.getAttribute("seq");
          if (this.seq != seq) {
            this.seq = seq;
            this.totalCount = item.getAttribute("total");
          }
        }

      
        //插入或者更新历史记录 { comicId: '456', chapter: '3', page: '2',comicTitle:'xxx',updateChapter:'xxx',icon:'xxx',author:'xxx' }
        history.insertOrUpdate({
          comicId: this.id,
          chapter: this.seq,
          page: this.currentIndex,
          comicTitle: this.title,
          updateChapter: this.total,
          icon: this.icon,
          author: this.authorName,
        });
      }
    },
    clickComic() {
      //点击漫画区域 打开关闭菜单
      this.hidden = !this.hidden;
      //关闭提示
      this.showScrollTips = false;
    },
    getTopItemIndex(scrollEl) {
      //获取当前的位置
      const scrollTop = scrollEl.scrollTop + scrollEl.clientHeight / 3;
      const listItems = scrollEl.getElementsByClassName("comic-pic-item");

      let left = 0;
      let right = listItems.length - 1;

      while (left <= right) {
        const mid = Math.floor((left + right) / 2);
        const offsetTop = listItems[mid].offsetTop;

        if (offsetTop < scrollTop) {
          left = mid + 1;
        } else if (offsetTop > scrollTop) {
          right = mid - 1;
        } else {
          return mid;
        }
      }

      return left > 0 ? left - 1 : 0;
    },
    back() {
      this.$router.push("/detail/" + this.id);
    },
    go2Chapter(chapter) {
      //关闭目录
      this.showlChapterList();
      this.seq = chapter.seq;
      this.currentIndex = 0;
      this.seqs = []; //清空当前的数据，
      this.loadComicContent(true);
    },
    showModal(msg) {
      this.message = msg;
      this.isModalVisible = true;
    },
    showSmallTips(msg) {
      this.smallTip = msg;
      this.showScrollTips = true;
    },
    onModalHide() {
      console.log("用户取消了操作");
      this.isModalVisible = false;
    },
    onModalOk() {
      console.log("用户确认了操作");
      this.isModalVisible = false;
      this.$router.push("/login");
    },
    sort() {
      if (this.busy) {
        return;
      }
      this.chapterOrderAsc = !this.chapterOrderAsc;
      this.page = 1;
      this.max = false;
      this.chapters = [];
      this.loadMoreChapter();
    },
    async loadMoreChapter() {
      //加载目录
      if (this.busy || this.max) {
        return;
      }
      this.busy = true;
      //loading
      this.isLoading = true;

      try {
        var data = (data = await apiUtils.loadReadChapters({
          p: this.page,
          o: this.chapterOrderAsc ? 1 : 2,
          d: this.id,
        }));
        //检查是已经购买的章节
        this.checkLock(data.dataList);
        const maxPage = Math.max(1, Math.ceil(data.total / 20));
        this.max = maxPage == this.page;
        this.busy = false;
        this.chapters = this.chapters.concat(data.dataList);
        this.page++;

        this.isLoading = false;
        //已经读过的要在第一个位置显示
        this.$nextTick(() => {
          const list = this.$refs.scroll.children;
          for (let i = 0; i < list.length; i++) {
            const item = list[i];
            if (item.querySelector(".now")) {
              item.scrollIntoView({ block: "start", behavior: "smooth" });
              break;
            }
          }
        });
      } catch (error) {
        this.busy = false;
      }
    },
    goTop() {
      //目录到顶方法
      this.$refs.scroll.scrollTo({
        top: 0,
        behavior: "smooth",
      });
    },
    goToCurrent(shoeTip = true) {
      //第一次加载后，要跳转到上次看到的图片
      // 如果当前传入的pid不是第一张图片的pid，那么需要先滑动到对应的图片位置
      const ulList = this.$refs.list.querySelectorAll(".comic-pic-list");
      const ul = ulList[0];
      const list = ul.children;

      const index = this.currentIndex - 2;

      if (index >= 0 && index < list.length) {
        const img = list[index];
        img.scrollIntoView();

        if (shoeTip) {
          //提示打开
          this.showSmallTips("已经为主人跳到上次看到的位置~");
        }
      }
    },
    // 绑定滚动事件
    bindScrollEvent() {
      //目录滚动事件
      const scrollEl = this.$refs.scroll;
      scrollEl.addEventListener("scroll", () => {
        if (
          scrollEl.scrollTop + scrollEl.clientHeight >=
          scrollEl.scrollHeight
        ) {
          this.loadMoreChapter();
        }
      });
    },
    async loadBuyList() {
      //实时更新已经购买的章节
      if (!apiUtils.checkLogin()) {
        console.log("no login");
        return;
      }

      const data = await apiUtils.loadChapterBuyList({ comicId: this.id });
      this.buyList = this.buyList.concat(data);
    },
    checkLock(items) {
      //校验章节是否购买了
      for (let index = 0; index < items.length; index++) {
        const element = items[index];
        if (element.lock == 1) {
          //是否解锁
          element.lock = this.buyList.indexOf(element.seq) > -1 ? 2 : 1;
        }
      }
    },
    async showlChapterList() {
      //第一次打开菜单  默认加载第一页数据 ，加载购买列表
      if (this.firstOpenMenu) {
        this.firstOpenMenu = false;
        console.log("firstOpenMenu");
        await this.loadBuyList();
      }

      //开关目录列表
      //展开目录
      var tmp = !this.show;

      if (tmp) {
        if (this.page == 0) {
          //通过当前章节数计算页码
          this.page = 1;

          this.loadMoreChapter();
        }
        this.show = tmp;
        setTimeout(() => {
          this.animate = true;
        }, 0);
      } else {
        this.animate = false;
        setTimeout(() => {
          this.show = tmp;
        }, 300);
      }
    },
    async gotoNext(num) {
      //loading
      if (this.busy) {
        return;
      }
      this.busy = true;

      //上一章、下一章
      this.seq = Number(this.seq) + num;

      await this.loadComicContent(num > 0);

      this.currentIndex = 0;

      //滑动到当前章节的第一页

      this.busy = false;

      //记录历史
      //下一帧加载
      if (this.loading == "none") {
        this.$nextTick(() => {
          //插入或者更新历史记录 { comicId: '456', chapter: '3', page: '2',comicTitle:'xxx',updateChapter:'xxx',icon:'xxx',author:'xxx' }
          history.insertOrUpdate({
            comicId: this.id,
            chapter: this.seq,
            page: 1,
            comicTitle: this.title,
            updateChapter: this.total,
            icon: this.icon,
            author: this.authorName,
          });
        });
      }
    },
    /**
     * 加载漫画数据
     */
    async loadComic() {
      this.loading = "block";
      //加载当前漫画数据
      const comicInfo = await apiUtils.loadComic({ comicId: this.id });
      this.title = comicInfo.title;
      this.authorName = comicInfo.authorName; //作者
      this.icon = comicInfo.icon; //图标
      this.status = comicInfo.status;
      this.total = comicInfo.total;
      this.tryNumber = comicInfo.tryNumber;
      this.loading = "none"; //关闭加载提示,
    },
    /**
     * 加载章节内容
     */
    async loadComicContent(concat) {
      this.loadNumerb = 0;
      this.isLoading = true;
      this.loading = "block";
      this.seqs = []; //清空当前的数据，
      //根据登陆情况查询章节内容
      var content = await apiUtils.loadComicContent({
        comicId: this.id,
        seq: this.seq,
        tk: apiUtils.token(),
      });

      //校验数据是否正确
      try {
        if (content.indexOf("err") > -1) {
          //数据异常
          return;
        }
      } catch (error) {
        console.log("start read comic ");
      }

      //根据方向插入新内容
      if (concat) {
        //插入到最后面
        this.seqs.push(content);
      } else {
        //插入到最前面
        this.seqs.unshift(content);
      }

      this.totalCount = content.totalCount; //当前章总页数量,
      this.seqTitle = content.title; //当前章节的标题
      this.price = content.price; //章节价格
      this.balance = content.balance; //携带用户的余额。如果未登陆就是0
      this.needBuy = content.needBuy == 1; // 章节是否需要购买 0 否1 是 2已购买
      this.chapterId = content.chapterId; //章节id
      this.loading = "none";
    },
    loadComplete() {
      //图片加载完成
      this.loadNumerb++;
      if (this.loadNumerb == this.totalCount) {
        this.isLoading = false;
      }
    },
    /**
     * 加载历史记录 如果当前章节和历史记录的章节一致，那么就跳转到历史记录的页码
     */
    async loadHistory() {
      //加载历史记录
      //{ comicId: '456', chapter: '3', page: '2',comicTitle:'xxx',updateChapter:'xxx',icon:'xxx',author:'xxx' }
      const hist = history.findReadingHistoryById(this.id);
      if (hist == null) {
        this.currentIndex = 0;
      } else {
        //如果当前章节和历史记录的章节一致，那么就跳转到历史记录的页码
        if (hist.chapter && hist.chapter == this.seq) {
          this.currentIndex = hist.page;
        } else {
          this.currentIndex = 0;
        }
      }
    },
    /**
     * 购买当前章节
     */
    async buythis() {
      try {
        if (this.busy) {
          return;
        }
        if(!apiUtils.checkLogin()){
          this.showSmallTips("请先登录");
          return;
        }
        this.loading = "block";
        this.busy = true;
        const data = await apiUtils.buy({ id: this.chapterId });
        if (data === undefined) {
          this.showSmallTips("购买成功！");
          this.seqs = [];
          this.loadComicContent(true);
          this.needBuy = false;
        } else {
          this.showSmallTips(data.msg);
        }

        this.busy = false;
        this.loading = "none";
      } catch (error) {
        this.showSmallTips("系统繁忙，请稍后重试");
        this.busy = false;
        this.loading = "none";
      }
    },
    toLogin() {
      this.$router.push("/login");
    },
  },
  created() {
    setInterval(() => {
      this.hidden = true;
      this.showScrollTips = false;
    }, 3500);
  },
};
</script>
