<!-- 历史记录页 -->
<template>
  <!-- loading -->
  <LoadingCmp ref="loading" />
  <!-- 顶部 -->
  <TopCmp :topTitle="title" />

  <article class="main under-top-bar">
    <div class="mod-his">
      <!-- 登陆提示 -->
      <section :class="{ 'his-normal': logined }">
         
        <div class="his-bgLayBox">
          <div class="his-login">
            <span>登录后同步云书架中的漫画</span>
            <a class="his-empty-login" @click="login">登录</a>
          </div>
        </div>
      </section>

      <!--头部tab切换部分 begin-->
      <section
        class="bookShelf-tab under-top-bar-fixed"
        v-if="this.historyList.length > 0"
      >
        <div class="tab-list">
          <!-- onEdit 开启编辑模式 -->
          <a
            id="tab_edit"
            :class="{ 'tab-edit': true, onEdit: onEdit }"
            @click="chooseEdit"
          ></a>
        </div>
      </section>
      <!-- 数据部分 -->
      <section class="his-normal" style="display: block">
        <ul class="his-list" id="his_list">
          <!-- choose 选择 unChoose 不选择  没有就不选择-->
          <li
            :class="['comic-item', item.choose]"
            v-for="item in historyList"
            :key="item.comicId"
          >
            <router-link
              :to="'/read/' + item.comicId + '/' + item.chapter"
              class="comic-link"
            >
              <div class="comic-cover">
                <img
                  class="cover-image"
                  :src="item.icon"
                  :alt="item.comicTitle"
                />
              </div>
              <div class="comic-info">
                <strong class="comic-title">{{ item.comicTitle }}</strong>
                <small class="comic-author">作者：{{ item.author }}</small>
                <small class="comic-update"
                  >更新到{{ item.updateChapter || "..." }}话</small
                >
                <div class="comic-continue">
                  <i class="continue-img"></i>
                  <small>续看第{{ item.chapter }}话</small>
                </div>
              </div>
            </router-link>

            <div class="comic-choose" @click="selectOne(item)">
              <i class="icon-choose"></i>
              <i class="icon-unChoose"></i>
            </div>
          </li>
        </ul>
      </section>

      <!--编辑状态下的工具栏 begin-->
      <div
        class="mod-edit"
        id="his_edit"
        v-bind:style="{ display: onEdit ? '' : 'none' }"
      >
        <!-- 全选后 delAll -->
        <a
          :class="{ 'edit-choose': true, delAll: delAll }"
          id="his_edit_choose"
        >
          <i class="edit-choose-normal"></i>
          <i class="edit-choose-all"></i>
          <span v-if="delAll" @click="selectAll(false)">取消全选</span>
          <span v-if="!delAll" @click="selectAll(true)">全选</span>
        </a>
        <a class="edit-del" id="his_edit_del" @click="delBtn">
          <i class="edit-del-normal"></i>
          <i class="edit-del-empty"></i>
          <span>删除</span>
        </a>
      </div>
    </div>
  </article>

  <!-- 提示组件 -->
  <TipCmp ref="tip" @loadMore="handleScroll" :cmp="'comicList'" />
  <!-- 返回顶部 -->
  <BackToTopCmp />
</template>
<script>
import TopCmp from "../common/TopCmp.vue";
import TipCmp from "../common/ListTipCmp.vue";
import BackToTopCmp from "../common/BackToTopCmp.vue";
import apiUtils from "../utils/ApiUtil.vue";
import historyUtils from "../utils/History.vue";
import { decryptImageUrl } from "../utils/AES.vue";
import LoadingCmp from "../common/LoadingCmp.vue";
export default {
  data() {
    return {
      title: "历史记录",
      historyList: [],
      //编辑模式
      onEdit: false,
      //全选了
      delAll: false,
      //是否登陆，
      logined: apiUtils.checkLogin(),
      currentPage: 0,
      pageSize: 5,
    };
  },
  methods: {
    async getHistoryData(page = 0) {
      //已经达到最大页了
      if (!this.$refs.tip.canScroll()) {
        return;
      }
      // loading
      this.$refs.loading.show();
      // //设置动效
      this.$refs.tip.loadingStart();
      try {
        var response = null;

        response = historyUtils.getPaginatedReadingHistory(page, this.pageSize);
        //解密图片
        await Promise.all(
          response.map(async (item) => {
            item.choose = false;
            item.icon = await decryptImageUrl(item.icon);
          })
        );

        this.historyList = this.historyList.concat(response);
        this.currentPage = page;
        const total = historyUtils.getReadingHistoryCount();
        //计算最大页
        const maxPage = Math.max(Math.ceil(total / this.pageSize), 1);

        //是否最大页
        this.$refs.tip.loadingEnd(
          this.currentPage + 1 == maxPage,
          this.historyList.length > 0
        );
        //隐藏loading
        this.$refs.loading.hide();
      } catch (error) {
        this.$refs.tip.hasError();
      }
    },
    handleScroll() {

      this.getHistoryData(this.currentPage + 1);
    },
    chooseEdit() {
      //进入或者退出编辑模式
      this.onEdit = !this.onEdit;
      //是否全选
      this.delAll = this.queryDelAll();
      //进入编辑模式
      this.enterEditMode();
    },
    queryDelAll() {
      //数据列表是否全选了
      for (let index = 0; index < this.historyList.length; index++) {
        const element = this.historyList[index];
        if (element.choose != "choose") {
          return false;
        }
      }
      return true;
    },
    enterEditMode() {
      //进入编辑模式
      for (let index = 0; index < this.historyList.length; index++) {
        const element = this.historyList[index];
        element.choose = this.onEdit ? "unChoose" : "";
      }
    },
    selectAll(selected) {
      //全选
      for (let index = 0; index < this.historyList.length; index++) {
        const element = this.historyList[index];
        element.choose = selected ? "choose" : "unChoose";
      }
      this.delAll = selected;
    },
    selectOne(element) {
      //选择或者取消选择一条数据
      element.choose = element.choose == "choose" ? "unChoose" : "choose";
      this.delAll = this.queryDelAll();
    },
    delBtn() {
      //删除按钮
      for (let i = 0; i < this.historyList.length; i++) {
        if (this.historyList[i].choose === "choose") {
          historyUtils.deleteReadingHistoryById(this.historyList[i].comicId);
          this.historyList.splice(i, 1);
          i--;
        }
      }

      //如果删除完了，退出编辑模式，
      if (this.historyList.length == 0) {
        this.onEdit = false;
        //刷新数据
        this.getHistoryData(0);
      }
    },
    login() {
      this.$router.push("/login");
    },
  },
  components: {
    TopCmp,
    TipCmp,
    BackToTopCmp,
    LoadingCmp,
  },
  mounted() {
    this.$setMetaData("历史记录");
    this.getHistoryData(0);
  },
};
</script>
<style scoped>
