<!-- 用户中心 -->
<template>
  <!-- 顶部 -->
  <TopCmp :topTitle="title" />
  <!-- 加载中 -->
  <LoadingCmp ref="loading" />

  <article class="main">
    <!--布局上的头部，包括 mod-nav，mod-head，mod-stratRead，mod-tab-->
    <div class="lay-head">
      <section class="mod-head">
        <div class="head-banner">
          <img :src="icon2" class="head-cover" alt="" />
        </div>
      </section>
    </div>

    <div class="lay-content">
      <!-- 作品详细信息 -->
      <section class="head-info">
        <div class="head-info-detail">
          <div class="head-title-tags">
            <h1>{{ title }}</h1>
            <div class="head-tags">
              <span v-for="tag in tags" :key="tag">{{ tag }}</span>
            </div>
          </div>
          <div class="head-info-grade">
            <p class="info-grade">{{ grade }}</p>
            <div class="info-star">
              <div class="star-wrap">
                <div class="star-light"></div>
                <div class="star"></div>
              </div>
            </div>
            <p class="info-member-rate">{{ memberRate }}人评分</p>
          </div>
        </div>
        <div class="head-info-desc">
          {{ desc }}
        </div>
        <div class="head-info-author">
          <span class="author">作者</span>
          <ul class="author-list">
            <li class="author-wr" v-for="author in authors" :key="author.name">
              <img :src="author.avatar" alt="" />
              {{ author.name }}
            </li>
          </ul>
        </div>
      </section>

      <!-- 广告 -->
      <section class="lay-guiding">
        <div class="app-guiding gui-content">
          <!-- <img class="app-guiding-img" src="static/picture/app-guiding.png" /> -->
        </div>
      </section>
      <!-- 中间部分的目录 -->
      <section class="chapter-info">
        <h1 class="mod-chapter-title">
          目录
          <span>
            {{ status }}，已更新{{ updatedChapter }}话 (可试读至{{
              tryReadChapter
            }}话)
          </span>
          <span class="chapter-more" @click="toggleChapterList">
            展开目录 <i class="icon-arrow-right"></i>
          </span>
        </h1>

        <div class="mod-chapter-list">
          <ul class="chapter-list chapter-horizonal" ref="modelChapterList">
            <li
              v-for="chapter in chapters"
              :key="chapter.seq"
              class="chapter-item"
              :class="{ now: chapter.seq == seq }"
            >
              <router-link
                :to="'/read/' + id + '/' + chapter.seq"
                class="chapter-link"
                :class="{ now: chapter.seq == seq }"
              >
                <img
                  :src="chapter.image"
                  :class="{
                    'chapter-img': true,
                  }"
                  alt=""
                />
                <p
                  class="chapter-title"
                  :class="{
                    lock: chapter.lock == 1,
                    unLock: chapter.lock == 2,
                  }"
                >
                  {{ chapter.seq }}.{{ chapter.title }}
                </p>
              </router-link>
            </li>
            <!-- 查看更多按钮 -->
            <li class="chapter-special-li">
              <div
                class="icon-show-more"
                id="icon_show_more"
                @click="toggleChapterList"
              ></div>
            </li>
          </ul>
        </div>
      </section>

      <!-- app广告 -->

      <section class="recomment-list">
        <!-- 近期热门推荐 -->
        <!-- <HotComicSixCmp /> -->
        <AdCmp />  
      </section>
    </div>
  </article>

  <!--底部工具栏 begin-->
  <section class="mod-toolBar" v-if="allLoaded">
    <div class="lay-flex">
      <div class="lay-box">
        <a id="btn_index" class="toolBar-toolIndex" href="/">
          <i class="icon-index">[前往书库]</i>
          <p class="iconfont" @click="home">返回主页</p>
        </a>
        <a
          id="btn_toolFav"
          v-bind:class="{
            'toolBar-toolFav': true,
            unfav: !login,
            faved: login,
          }"
          @click="collection"
          >{{ collectionName }}</a
        >
      </div>
      <router-link
        :to="'/read/' + id + '/1'"
        class="toolBar-toolRead"
        v-if="this.seq == 1"
        >开始阅读</router-link
      >
      <router-link
        :to="'/read/' + id + '/' + this.seq"
        class="toolBar-toolRead"
        v-if="this.seq > 1"
        >续看{{ seq }}话</router-link
      >
    </div>
  </section>
  <!--底部工具栏 end-->

  <!-- 返回顶部按钮 -->
  <BackToTopCmp />

  <!-- 底部的目录页 show animate -->
  <section
    :class="{ 'bottom-chapter-list': true, show: isShow, animate: isAnimate }"
  >
    <div class="bottom-chapter-mask"></div>
    <div class="bottom-chapter-wrap">
      <div class="wrap-top">
        <h2>
          目录
          <span class="bottom-list-close-btn" @click="toggleChapterList"
            ><i></i
          ></span>
        </h2>
        <div class="wrap-header">
          共{{ updatedChapter }}话 (可试读至{{ tryReadChapter }}话)
          <div class="asc-desc">
            <span :class="{ active: sortAsc }" id="order_asc" @click="sort"
              >正序</span
            >
            |
            <span :class="{ active: !sortAsc }" @click="sort" id="order_desc"
              >倒序</span
            >
          </div>
        </div>
      </div>
      <span class="bottom-back-to-top bottom-icon to-top" @click="goTop"
        ><i></i><span>到顶</span></span
      >
      <div class="full-height-container">
        <ul
          class="chapter-wrap-list normal"
          ref="scroll"
          style="height: 350px; overflow-y: scroll"
        >
          <li
            v-for="(item, index) in items"
            :key="index"
            class="bottom-chapter-item"
          >
            <router-link
              :to="'/read/' + id + '/' + item.seq"
              class="chapter-link"
              :class="{ now: item.seq == seq }"
            >
              <div class="comic-cover">
                <img
                  :class="{
                    'cover-image': true,
                    lock: item.lock == 1,
                    unLock: item.lock == 2,
                  }"
                  :src="item.image"
                />
              </div>
              <div class="comic-info">
                <a
                  :class="{
                    lock: item.lock == 1,
                    unLock: item.lock == 2,
                  }"
                >
                  <span class="comic-title"
                    >{{ item.seq }}.{{ item.title }}</span
                  >
                  <i :class="icon - locate"></i>
                </a>
              </div>
            </router-link>
          </li>
        </ul>
      </div>
      <loading-spinner v-if="isLoading" />
      <div v-if="busy">Loading more items...</div>
      <div v-if="max">少爷，已经到底了...</div>
      <div v-if="err">再试一次...</div>
    </div>
  </section>
  <!-- 提示框 -->
  <modal-confirm
    v-if="isModalVisible"
    message="登陆后可使用收藏服务，跳转到登陆页面？"
    cancel-text="再想想"
    ok-text="立即登陆"
    @hide="onModalHide"
    @ok="onModalOk"
  ></modal-confirm>
</template>
<script>
//import HotComicSixCmp from "../common/HotComicSixCmpForDetail.vue";
import TopCmp from "../common/TopCmp.vue";
import BackToTopCmp from "../common/BackToTopCmp.vue";
import LoadingCmp from "../common/LoadingCmp.vue";
import apiUtil from "../utils/ApiUtil.vue";
import history from "../utils/History.vue";
import { decryptImageUrl } from "../utils/AES.vue";
import LoadingSpinner from "../common/LoadingCmp2.vue";
import AdCmp from "../common/AdCmp.vue";
export default {
  data() {
    return {
      id: this.$route.params.id,
      title: "",
      cover: "",
      items: [],
      buyList: [],
      isShow: false,
      isAnimate: false, //是否展示更多菜单
      sortAsc: true, //正序排序,
      page: 1,
      busy: false,
      max: false,
      err: false,
      seq: 0,
      fav: false,
      collectionName: "收藏",
      isModalVisible: false,
      login: apiUtil.checkLogin(),
      allLoaded: false,
      firstLoadChapter: true,
      isLoading: false,
    };
  },
  components: {
    TopCmp,
    LoadingCmp,
    // HotComicSixCmp,
    BackToTopCmp,
    LoadingSpinner,
    AdCmp,
  },
  async mounted() {
    this.$refs.loading.show();
    //查询阅读历史
    var his = await history.findReadingHistoryById(this.id);
    if (his == null) {
      //  { comicId: '456', chapter: '3', page: '2',comicTitle:'xxx',updateChapter:'xxx',icon:'xxx',author:'xxx' }
      his = {
        comicId: this.id,
        chapter: 1,
        page: 1,
        comicTitle: "",
        updateChapter: 1,
        icon: "",
      };
    }

    //加载详情信1
    await this.loadComicInfo(his.chapter);
    //设置当前章节
    this.seq = Math.min(his.chapter, this.updatedChapter);

    //设置页面标题
    this.$setMetaData(this.title);

    //更新阅读历史 这里只对存在的历史记录进行漫画信息的更新 不存在的不更新
    his.comicTitle = this.title;
    his.updateChapter = this.updatedChapter;
    his.icon = this.icon3;
    his.author = this.authors[0].name;
    history.updateReadingHistoryDetails(his);
    //绑定滚动事件
    this.bindScrollEvent();
    //加载章节列表
    //  this.loadMoreChapter();

    //关闭加载提示
    this.$refs.loading.hide();

    this.allLoaded = true;
    //已经读过的要在第一个位置显示
    this.$nextTick(() => {
      const ul = this.$refs.modelChapterList;
      //查询当前章节
      const current = ul.querySelector(".now");
      if (current) {
        ul.scrollLeft = current.offsetLeft;
      }
    });
  },
  methods: {
    toggleChapterList() {
      //加载数据
      if (this.firstLoadChapter) {
        this.loadMoreChapter();
        this.firstLoadChapter = false;
      }
      //展开目录
      var tmp = !this.isShow;

      if (tmp) {
        this.isShow = tmp;
        setTimeout(() => {
          this.isAnimate = true;
        }, 0);
      } else {
        this.isAnimate = false;
        setTimeout(() => {
          this.isShow = tmp;
        }, 300);
      }
    },
    sort() {
      this.sortAsc = !this.sortAsc;
      this.page = 1;
      this.items = [];
      this.max = false;
      this.loadMoreChapter();
    },
    //加载漫画详情信息
    async loadComicInfo(chapger) {
      var info = await apiUtil.getComicInfo({
        comicId: this.id,
        page: chapger, //阅读到第几章
      });
      this.fav = false;
      if (apiUtil.checkLogin()) {
        //已经登陆 查询是否收藏 1已收藏 0未收藏
        //加载购买记录
        await this.loadBuyList();
        this.fav =
          (await apiUtil.getCollectionStatus({ comicId: this.id })) == 1;
      }

      this.title = info.title; //标题
      this.icon = info.icon;
      this.icon3 = info.icon2; //竖图
      //解密图片地址
      this.icon2 = await decryptImageUrl(info.icon); //封面

      this.tags = info.tags; //[]
      this.grade = info.grade; //评分
      this.memberRate = info.memberRate; //会员评分
      this.desc = info.desc; //简介
      this.authors = info.authors; //name avatar
      //解密图片
      await Promise.all(
        this.authors.map(async (item) => {
          item.avatar = await decryptImageUrl(item.avatar);
        })
      );
      this.status = info.status == 1 ? "连载中" : "已完结"; //状态
      this.updatedChapter = info.updatedChapter; //最新章节
      this.tryReadChapter = info.tryReadChapter; //试读章节

      this.checkLock(info.chapters);
      this.chapters = info.chapters; //章节列表
      //解密图片
      await Promise.all(
        this.chapters.map(async (item) => {
          item.image = await decryptImageUrl(item.image);
        })
      );

      if (this.fav) {
        this.collectionName = "取消收藏";
      }
    },
    async loadMoreChapter() {
      //加载目录
      if (this.busy || this.max) {
        return;
      }
      //打开Loading
      this.isLoading = true;
      this.busy = true;
      this.err = false;
      try {
        const data = await apiUtil.loadMoreChapter({
          comicId: this.id,
          page: this.page,
          sortAsc: this.sortAsc ? 1 : 2,
        });
        const maxPage = Math.max(1, Math.ceil(data.total / 10));
        this.max = maxPage == this.page;
        this.busy = false;
        this.checkLock(data.dataList);
        //解密
        await Promise.all(
          data.dataList.map(async (item) => {
            item.image = await decryptImageUrl(item.image);
          })
        );
        this.items = this.items.concat(data.dataList);
        //关闭Loading
        this.isLoading = false;
        this.page++;
      } catch (error) {
        this.busy = false;
        this.err = true;
      }
    },
    async loadBuyList() {
      //实时更新已经购买的章节
      if (!apiUtil.checkLogin()) {
        console.log("no login");
        return;
      }

      const data = await apiUtil.loadChapterBuyList({ comicId: this.id });
      this.buyList = this.buyList.concat(data);
    },
    checkLock(items) {
      //校验章节是否购买了
      for (let index = 0; index < items.length; index++) {
        const element = items[index];
        if (element.lock == 1) {
          //是否解锁
          element.lock = this.buyList.indexOf(element.seq) > -1 ? 2 : 1;
        }
      }
    },
    goTop() {
      //到顶
      this.$refs.scroll.scrollTo({
        top: 0,
        behavior: "smooth",
      });
    },

    // 绑定滚动事件
    bindScrollEvent() {
      const scrollEl = this.$refs.scroll;
      scrollEl.addEventListener("scroll", () => {
        if (
          scrollEl.scrollTop + scrollEl.clientHeight >=
          scrollEl.scrollHeight
        ) {
          this.loadMoreChapter();
        }
      });
    },
    home() {
      this.$router.push("/");
    },
    collection() {
      // 收藏
      //没有登陆
      if (!apiUtil.checkLogin()) {
        this.showModal();
        return;
      }

      this.$refs.loading.show();
      //取消收藏
      if (this.fav) {
        apiUtil.delCollection({ ids: [this.id] }).then(() => {
          this.fav = !this.fav;
          this.$refs.loading.hide();
          this.collectionName = "收藏";
        });
      } else {
        apiUtil.addCollection({ id: this.id }).then(() => {
          this.fav = !this.fav;
          this.collectionName = "取消收藏";
          this.$refs.loading.hide();
        });
      }
    },
    showModal() {
      this.isModalVisible = true;
    },
    onModalHide() {
      console.log("用户取消了操作");
      this.isModalVisible = false;
    },
    onModalOk() {
      console.log("用户确认了操作");
      this.isModalVisible = false;
      this.$router.push("/login");
    },
  },
};
</script>
<style scoped>
.list {
  height: 400px;
  overflow-y: scroll;
  margin-bottom: 20px;
}
.chapter-list.chapter-horizonal {
  display: flex;
  overflow-x: auto;
  overflow-y: hidden;
  white-space: nowrap;
  scrollbar-width: thin;
  -webkit-overflow-scrolling: touch;
}

.chapter-item {
  flex-shrink: 0;
}

.chapter-wrap-list.normal {
  overscroll-behavior: contain;
}

.full-height-container {
  display: flex;
  flex-direction: column;
  height: 100%; /* 或其他您希望设置的高度 */
}

.chapter-wrap-list.normal {
  flex-grow: 1;
  overflow-y: scroll;
}
</style>
