<script>
import CryptoJS from "crypto-js";
import axios from "axios";
import apiUtils from "./ApiUtil.vue";

async function getImageDataAsBase64(imageUrl) {
  const response = await axios.get(imageUrl, { responseType: "text" });
  return response.data;
}

function decryptImageData(data, key, iv) {
  const keyHex = CryptoJS.enc.Utf8.parse(key);
  const ivHex = CryptoJS.enc.Utf8.parse(iv);
  const decrypted = CryptoJS.AES.decrypt(data, keyHex, {
    iv: ivHex,
    mode: CryptoJS.mode.CBC,
    padding: CryptoJS.pad.Pkcs7,
  });
  return decrypted.toString(CryptoJS.enc.Utf8);
}

/**
 * 解密图片
 */
export async function decryptImageUrl(imageUrl) {
  // 获取系统配置
  const data = await apiUtils.getSystemConfig();
  // 判断是否开启图片加密
  if (data.imageEncryptionEnabled == 1) {
    const cachedImageData = localStorage.getItem(imageUrl);
    if (cachedImageData) {
      return cachedImageData;
    }

    // 获取图片数据
    const imageData = await getImageDataAsBase64(imageUrl);
    // 解密图片数据
    const decryptedImageData = decryptImageData(
      imageData,
      data.imageEncryptionKey,
      data.imageEncryptionIv
    );

    return decryptedImageData;
  } else {
    return imageUrl;
  }
}

/**
 * 解密数据
 */
export async function decryptData(data) {
  // 获取系统配置
  const config = await apiUtils.getSystemConfig();
  // 判断是否开启数据加密
  if (config.dataEncryptionEnabled == 1) {
    // 解密数据数据
    const decryptedImageData = decryptImageData(
      data,
      config.dataEncryptionKey,
      config.dataEncryptionIv
    );
    return JSON.parse(decryptedImageData);
  } else {
    return data;
  }
}

/**
 * 解密固定数据
 */

export async function decryptFixedData(data) {
  // 解密数据数据
  const decryptedImageData = decryptImageData(
    data,
    "2003200420052006",
    "2003200420052006"
  );
  return JSON.parse(decryptedImageData);
}

export default {
  setup() {
    return {
      decryptImage: decryptImageUrl,
    };
  },
};
</script>
