<template>
  <!-- loading -->
  <LoadingCmp ref="loading" />
  <!-- 顶部 -->
  <TopCmp :topTitle="title" />
  <!-- 顶部的分类 -->
  <section class="rank-tab under-top-bar">
    <div class="tab-list">
      <a
        v-for="tab in tabs"
        :key="tab.type"
        class="tab-list-item"
        :class="{ active: tab.isActive }"
        @click="selectTab(tab)"
        :data-type="tab.type"
        >{{ tab.text }}</a
      >
    </div>
  </section>
  <!-- 前三 -->
  <section class="rank-top3" v-if="top3List.length >2">
    <div class="top3-box active">
      <!-- 如果index == 1，那么就是第2名，就是top3-box-item2，。如果 index == 2，那么就是第1名，就是top3-box-item1
        如果index ==3 ，那么就是第3名，就是top3-box-item3 -->
      <div
        v-for="item in top3List"
        :key="item.id"
        :class="`top3-box-item${item.index == 1 ? 2 : item.index == 2 ? 1 : 3}`"
      >
        <router-link :to="'/detail/' + item.id" class="comic-link">
          <img class="comic-cover" :src="item.cover" :alt="item.title" />
          <strong class="comic-title">{{ item.title }}</strong>
          <small class="comic-update">更新到{{ item.update }}话</small>
        </router-link>
      </div>
    </div>
  </section>
  <!-- 后面的普通榜单 -->
  <section class="rank-orderedList">
    <div class="comic-list-box active datas">
      <ul class="comic-list" id="list_rise">
        <li v-for="comic in comicList" :key="comic.id" class="comic-item">
          <router-link class="comic-link" :to="'/detail/' + comic.id">
            <div class="comic-cover">
              <img class="cover-image" :src="comic.cover" />
            </div>
            <div class="comic-info">
              <strong class="comic-title">{{ comic.title }}</strong>
              <small class="comic-update">更新到{{ comic.update }}话</small>
              <small class="comic-tag">{{ comic.tag }}</small>
              <small class="comic-desc">{{ comic.desc }}</small>
              <i class="comic-index">{{ comic.index }}</i>
            </div>
          </router-link>
        </li>
      </ul>
    </div>
  </section>
  <!-- 提示组件 -->
  <TipCmp ref="tip" />
  <!-- 返回顶部 -->
  <BackToTopCmp />
</template>

<script>
import TopCmp from "../common/TopCmp.vue";
import TipCmp from "../common/ListTipCmp.vue";
import BackToTopCmp from "../common/BackToTopCmp.vue";
import apiUtils from "../utils/ApiUtil.vue";
import LoadingCmp from "../common/LoadingCmp.vue";
import { decryptImageUrl } from "../utils/AES.vue";
export default {
  data() {
    return {
      tabs: [
        { text: "收藏榜", type: "favorites", isActive: true },

        { text: "新作榜", type: "create_date", isActive: false },
      ],
      selectedTabType: "favorites",
      title: "排行榜",
      top3List: [],
      comicList: [],
    };
  },
  components: {
    TopCmp,
    TipCmp,
    BackToTopCmp,
    LoadingCmp,
  },
  methods: {
    selectTab(type) {
      var t = type.type;

      if (this.selectTab == t) {
        return;
      }
      this.selectedTabType = t;
      this.tabs.forEach((tab) => {
        tab.isActive = tab.type == t;
      });
      this.top3List = [];
      this.comicList = [];

      this.$refs.tip.reset();
      this.getComicData(1);
    },
    async getComicData(page = 1, pageSize = 8) {
      //已经达到最大页了
      if (!this.$refs.tip.canScroll()) {
        return;
      }
      //loading
      this.$refs.loading.show();
      //设置动效
      this.$refs.tip.loadingStart();
      try {
        const response = await apiUtils.getRank({
          p: page,
          s: pageSize,
          o: this.selectedTabType,
        });
        //解密图片地址
        await Promise.all(
          response.dataList.map(async (item) => {
            item.cover = await decryptImageUrl(item.cover);
          })
        );

        //加上序号，并且将前三的数据单独拿出来
        response.dataList.forEach((item, index) => {
          //序号
          item.index = (page - 1) * pageSize + index + 1;
          //大于3的数据
          if (response.dataList.length >= 3) {
            if (item.index <= 3) {
              //前三的数据
              this.top3List.push(item);
            }
          }
        });
        if (page == 1) {
          //前三的数据排序
          this.top3List.sort((a, b) => {
            return a.index - b.index;
          });
          //从dataList中删除前三的数据
          if (response.dataList.length > 3) {
            response.dataList.splice(0, 3);
          }
        }

        //第一页返回前三数据，之后就不会有前三的数据了
        this.comicList = this.comicList.concat(response.dataList);

        this.currentPage = page;
       // const maxPage = Math.max(1, Math.ceil(response.total / pageSize));
        //是否最大页
        this.$refs.tip.loadingEnd(
          true,
          this.comicList.length > 0
        );
        //隐藏loading
        this.$refs.loading.hide();
      } catch (error) {
        this.$refs.tip.hasError();
      }
    },
    handleScroll() {
      const scrollHeight = document.body.scrollHeight;
      const scrollTop = document.body.scrollTop;
      const clientHeight = document.body.clientHeight;
      if (scrollTop + clientHeight >= scrollHeight) {
        this.getComicData(this.currentPage + 1);
      }
    },
  },
  mounted() {
    this.$setMetaData("排行榜");
    this.getComicData(1);
    window.addEventListener("scroll", this.handleScroll);
  },
  unmounted() {
    window.removeEventListener("scroll", this.handleScroll);
  },
};
</script>
<style scoped>
.rank-top3 .top3-box.active {
  display: flex;
  background-color: #f5f5ee;
}

.datas {
  background-color: #f5f5ee;
}
</style>
