<!-- 普通分类数据组件 -->
<!-- 推荐分类列表 -->
<template>
  <section class="category-normal">
    <ul class="category-normal-ul">
      <li
        class="category-normal-item"
        v-for="item in categories"
        :key="item.id"
      >
        <router-link
          :to="'/comicList/' + item.name + '/' + item.id"
          class="item-link"
        >
          <span class="item-name">| {{ item.name }}</span>
          <img class="item-cover" :src="item.cover" />
        </router-link>
      </li>
    </ul>
  </section>
</template>

<script>
import apiUtils from "../utils/ApiUtil.vue";
import { decryptImageUrl } from "../utils/AES.vue";
export default {
  data() {
    return {
      categories: [],
    };
  },
  created() {
    this.loadData();
  },
  methods: {
    loadData() {
      apiUtils.getCat().then(async (data) => {
        this.categories = data;
        // 解密图片地址
        await Promise.all(
          this.categories.map(async (item) => {
            item.cover = await decryptImageUrl(item.cover);
          })
        );

        this.$emit("loaded");
      });
    },
  },
};
</script>
<style scoped></style>
