<template>
  <header :class="{ 'top-bar': true, 'no-title': noTitle }">
    <a class="btn-top back" @click="back">返回</a>
    <h1 class="top-title">{{ topTitle }}</h1>
    <div class="top-logo">
      <img class="fuck" src="../../assets/Logo3.png" />
    </div>
    <div class="padding"></div>
    <a class="btn-top search" @click="gotoSearch">搜索</a>
    <a class="btn-top menu" @click="toggleMenu">菜单</a>
  </header>

  <!-- //菜单 -->
  <MenuCmp :show="showMenu" ref="menu" />
</template>

<script>
import MenuCmp from "./MenuCmp.vue";
export default {
  props: ["topTitle", "backUrl"],
  data() {
    return {
      showMenu: false,
      noTitle: this.topTitle == "",
    };
  },
  methods: {
    toggleMenu() {
      this.$refs.menu.setShow();
    },
    gotoSearch() {
      this.$router.push("/search");
    },
    back() {
      if (this.backUrl) {
        this.$router.push(this.backUrl);
      } else {
        this.$router.back();
      }
    },
  },
  components: {
    MenuCmp,
  },
  watch: {
    topTitle: {
      handler(newVal) {
        this.noTitle = newVal == "";
      },
    },
  },
};
</script>

