<!-- 返回顶部按钮 -->
<template>
    <div class="back-to-top2023">
        <a :class="{ 'back-to-top': true, 'show': show }" @click="scrollToTop" />
    </div>
</template>

<script>
export default {
    data() {
        return {
            show: false,
        };
    },
    mounted() {
        window.addEventListener("scroll", this.handleScroll);
    },
    methods: {
        handleScroll() {
            if (window.scrollY > 300) {
                this.show = true;
            } else {
                this.show = false;
            }
        },
        scrollToTop() {
            window.scrollTo({
                top: 0,
                behavior: 'smooth',
            });
        },
    },
    beforeUnmount() {
        window.removeEventListener("scroll", this.handleScroll);
    },
};
</script>

<style>
.back-to-top2023 {
    position: absolute;
    bottom: 20px;
    right: 20px;
}

@media (min-width: 768px) {
    .back-to-top {
        display: block;
    }
}
</style>

