<!-- 提示 -->
<template>
    <div class="modal-confirm-wrap" :class="{ show: isShow }">
        <div class="modal-confirm-container">
            <div class="compliance-dialog-icon"></div>
            <i class="icon-confirm-close-btn" @click="hide"></i>
            <div class="modal-confirm-content">
                <div class="content">{{ message }}</div>
            </div>
            <div class="confirm-btn-group">
                <div class="btn btn-cancel" @click="hide">{{ cancelText }}</div>
                <div class="btn btn-ok" @click="ok">{{ okText }}</div>
            </div>
        </div>
    </div>
</template>

<script>
export default {
    props: {
        message: {
            type: String,
            required: true
        },
        cancelText: {
            type: String,
            default: "再想想"
        },
        okText: {
            type: String,
            default: "立即打开"
        }
    },
    data() {
        return {
            isShow: true
        };
    },
    methods: {
        show() {
            this.isShow = true;
        },
        hide() {
            this.isShow = false;
            this.$emit('hide');
        },
        ok() {
            this.isShow = false;
            this.$emit('ok');
        }
    }
};
</script>
