<template>
  <section class="search-top-bar">
    <form class="search-form" @submit.prevent="search">
      <a class="search-top-btn back" @click="back">返回</a>
      <!--搜索栏文本-->
      <div v-bind:class="{ 'search-txt': true, 'focus': focus }">
        <!--搜索栏文本 - 文本提示框-->
        <div class="search-hint-box ">
          <div class="search-hint-pad focus-ani"></div>
          <i class="search-hint-icon">搜</i>
          <span class="search-hint-txt">漫画名|作者</span>
          <div class="search-hint-pad"></div>
        </div>
        <!--搜索栏文本 - 文本输入框-->
        <div class="search-text-box">
          <div class="txt-input-pad"></div>
          <input class="txt-input" type="search" autocapitalize="off" autocorrect="off"  
            @focus="inputFocus" @blur="onblur" @input="onchange" maxlength="10" v-model="queryKey" />
          <a class="txt-input-clear" @click="clear">X</a>
        </div>
      </div>
      <input class="search-top-btn search" type="submit" value="搜索">
    </form>
  </section>

  <!-- 搜索智能提示 -->
  <section class="search-smart-hint">
    <ul class="hint-list">
      <li class="item" v-for="item in resultData" :key="item.id">
        <router-link class="link" :to="{ path: '/detail/' + item.id }">{{ item.title }}</router-link>
      </li>
    </ul>
  </section>
</template>

<script>
// import apiUtils from '../utils/ApiUtil.vue';
export default {
  data() {
    return {
      queryKey:"",
      focus: false,
      resultData: []
    };
  },
  methods: {
    search() {
      if(!this.queryKey || this.queryKey ==''){
        return
      }
      //搜索列表页面
      this.$router.push(`/searchResult/${this.queryKey}`);
    },
    clear() {
      this.queryKey = '';
      this.focus = false;
      this.resultData = [];
    },
    back() {
 
      this.$router.push("/")
      this.resultData = [];
    },
    inputFocus() {
      this.focus = true;
    },
    onblur() {
      //失去焦点
      if (this.queryKey == '' || !this.queryKey) {
        this.focus = false;
      }

    },
    onchange(e) {
      this.queryKey = e.currentTarget.value;
      //去除空格
      this.queryKey = this.queryKey.replace(/\s+/g, "");
      if (this.queryKey && this.queryKey != '') {
        this.fetch();
      } else {
        this.resultData = [];
      }

    },
    fetch() {
      //智能搜索提示 todo   
      // apiUtils.smartQuery({ 'key': this.queryKey })
      //   .then(data => {
      //       this.resultData = data;
        
      //   })
      //   .catch(error => {
      //     console.error(error);
      //   });
    }
  }
};
</script>
 
