<!-- 充值记录 订单列表 -->
<template>
  <!-- loading -->
  <LoadingCmpVue ref="loading" />
  <!-- 顶部 -->
  <TopCmp :topTitle="title" />

  <article class="main under-top-bar">
    <div class="mod-his">
      <!-- 数据部分 -->
      <section class="his-normal" style="display: block">
        <ul class="his-list" id="his_list">
          <!-- choose 选择 unChoose 不选择  没有就不选择-->
          <li
            :class="['comic-item', item.choose]"
            v-for="item in historyList"
            :key="item.id"
          >
            <a class="comic-link">
              <div class="comic-info">
                <strong class="comic-title">{{ item.title }}</strong>
                <small class="comic-author"
                  >状态：{{ item.status || "未支付" }}</small
                >
                <small class="comic-update"
                  >创建时间：{{ item.createDate || "..." }}</small
                >
                <small class="comic-update"
                  >支付时间：{{ item.payDate || "..." }}</small
                >
                <div class="comic-continue">
                  <small>{{ item.price || "..." }}元 </small>
                </div>
              </div>
            </a>
          </li>
        </ul>
      </section>
    </div>
  </article>

  <!-- 提示组件 -->
  <TipCmp ref="tip"  @loadMore="handleScroll"/>
  <!-- 返回顶部 -->
  <BackToTopCmp />
</template>
<script>
import TopCmp from "../common/TopCmp.vue";
import TipCmp from "../common/ListTipCmp.vue";
import BackToTopCmp from "../common/BackToTopCmp.vue";
import apiUtils from "../utils/ApiUtil.vue";
import LoadingCmpVue from "../common/LoadingCmp.vue";
export default {
  data() {
    return {
      title: "充值记录",
      historyList: [],
    };
  },
  methods: {
    async getComicData(page = 1, pageSize = 7) {
      //已经达到最大页了
      if (!this.$refs.tip.canScroll()) {
        return;
      }
      //loading
      this.$refs.loading.show();
      // //设置动效
      this.$refs.tip.loadingStart();
      try {
        // var response = await api.get(`../assets/payList.json?page=${page}&pageSize=${pageSize}`);
        var data = await apiUtils.getOrderList({ page: page, size: pageSize });
        this.historyList = this.historyList.concat(data.dataList);
        this.currentPage = page;
        const maxPage = Math.max(Math.ceil(data.total / pageSize), 1);
        //是否最大页
        this.$refs.tip.loadingEnd(
          this.currentPage == maxPage,
          this.historyList.length > 0
        );
        //隐藏加载动画
        this.$refs.loading.hide();
      } catch (error) {
        this.$refs.tip.hasError();
      }
    },
    handleScroll() {
     this.getComicData(this.currentPage + 1);
    },
  },
  components: {
    TopCmp,
    TipCmp,
    BackToTopCmp,
    LoadingCmpVue,
  },
  mounted() {
    this.$setMetaData("充值记录", "超级漫画牛逼网", "我艹这牛逼");
    this.getComicData(1);
     
  },
  unmounted() {
    
  },
};
</script>
