<!-- 搜索页面 -->
<template>
  <LoadingCmp ref="loading" v-if="valid" />
  <div class="bodySearchFuck" v-if="valid">
    <!-- 搜索页头部 -->
    <keep-alive>
      <component :is="top" @loaded="onChildLoaded" />
    </keep-alive>
    <!-- 热门搜索 -->
    <keep-alive>
      <component :is="hot" @loaded="onChildLoaded" />
    </keep-alive>
  </div>
</template>
<script>
import top from "./SearchTopCmp.vue";
import hot from "./HotSearchCmp.vue";
import LoadingCmp from "../common/LoadingCmp.vue";

export default {
  data() {
    return {
      loadedCount: 0,
      hot: "hot",
      top: "top",
    };
  },
  components: {
    top,
    hot,
    LoadingCmp,
  },
  mounted() {
    this.$setMetaData("搜索");
    this.$refs.loading.show();
  },
  methods: {
    onChildLoaded() {
      this.loadedCount++;
      if (this.loadedCount >= 1) {
        this.$refs.loading.hide();
      }
    },
  },
  computed: {
    isMobile() {
      const userAgent = navigator.userAgent;
      const mobileRegex =
        /Mobi|Android|iPhone|iPad|iPod|BlackBerry|Windows Phone/i;
      return mobileRegex.test(userAgent);
    },
    isCrawler() {
      const userAgent = navigator.userAgent;
      const crawlerRegex = /bot|googlebot|crawler|spider|robot|crawling/i;
      return crawlerRegex.test(userAgent);
    },
    valid() {
      return !this.isCrawler && this.isMobile;
    },
  },
};
</script>
