<script>
// 推荐工具类
import apiUtils from "./ApiUtil.vue";
import { decryptImageUrl } from "./AES.vue";
const recommendUtils = {
  //获取数据
  getData: async function (type) {
    //获取数据
    const data = await apiUtils.getRecommend(type);
    //解密图片 阻塞等待
    if (data) {
      await Promise.all(
        data.list.map(async (item) => {
          item.cover = await decryptImageUrl(item.cover);
        })
      );
    }

    return data;
  },

  //获取bannner
  getBanner: function () {
    return this.getData("banner");
  },
  // 精品-好康打滚推荐！4 type = hot4
  getHot4: function () {
    return this.getData("hot4");
  },
  //   好康，真的非常好康！ 4 type = nice4
  getNice4: function () {
    return this.getData("nice4");
  },

  //超热门推荐！ 6 type = hot6
  getHot6: function () {
    return this.getData("hot6");
  },
  //  近期热门推荐 6 type = recent6
  getRecent6: function () {
    return this.getData("recent6");
  },
  //  [大家都在搜] 搜索的推荐内容 —- 特殊4 type = search4
  getSearch4: function () {
    return this.getData("search62");
  },
  // 大家都在搜 搜索内容为空时的推荐 4 type = search42
  getSearch42: function () {
    return this.getData("search42");
  },
};

export default recommendUtils;
</script>
