<!-- 首页顶部菜单 -->
<template>
  <div
    v-bind:class="{ 'nav-menu-box': true, show: isShow }"
    v-bind:style="{ display: display }"
  >
    <div class="nav-menu-bg" @click="setShow"></div>
    <div class="nav-menu">
      <ul class="nav-list">
        <li class="item" :data-button-id="10031">
          <a class="link" @click="clickIndex">
            <i class="icon index">[首页]</i>
            <span class="name">首页</span>
          </a>
        </li>
        <li class="item" v-cloak :data-button-id="10006" @click="clickCategory">
          <a class="link" href="javascript:;">
            <i class="icon category">[分]</i>
            <span class="name">分类</span>
          </a>
        </li>
        <li class="item" :data-button-id="10007" @click="clickRank">
          <a class="link" href="javascript:;">
            <i class="icon rank">[奖]</i>
            <span class="name">排行</span>
          </a>
        </li>
        <li class="item" :data-button-id="10003" @click="clickBookshelf">
          <a class="link top-bar-collect" href="javascript:void(0)">
            <i class="icon collect">[袋]</i>
            <span class="name">收藏</span>
          </a>
        </li>
        <li class="item" :data-button-id="10004" @click="clickHistory">
          <a class="link" href="javascript:;">
            <i class="icon history">[钟]</i>
            <span class="name">历史</span>
          </a>
        </li>
        <li class="item" :data-button-id="10008" @click="clickSupport">
          <a class="link support" href="javascript:void(0);">
            <i class="icon feedback">[反馈]</i>
            <span class="name">帮助</span>
          </a>
        </li>
      </ul>
      <a
        class="btn-home"
        href="javascript:void(0)"
        :data-button-id="10009"
        @click="clickUserCenter"
        >[个人中心]</a
      >
    </div>
  </div>
  <!-- 提示框 -->
  <modal-confirm
    v-if="isModalVisible"
    message="登陆后可使用该服务，跳转到登陆页面？"
    cancel-text="再想想"
    ok-text="立即登陆"
    @hide="onModalHide"
    @ok="onModalOk"
  ></modal-confirm>
</template>
<script>
import apiUtils from "../utils/ApiUtil.vue";
export default {
  props: ["show"],

  data() {
    return {
      isShow: this.show,
      display: "none",
      isModalVisible: false,
      currentUrl: "",
    };
  },
  methods: {
    clickIndex() {
      this.setShow();
      this.$router.push("/");
    },
    clickCategory() {
      this.setShow();
      // 添加点击分类按钮的方法
      this.$router.push("/category");
    },
    clickRank() {
      this.setShow();
      // 添加点击排行按钮的方法
      this.$router.push("/rank");
    },
    clickBookshelf() {
      // 添加点击收藏按钮的方法
      if (apiUtils.checkLogin()) {
        this.setShow();
        this.$router.push("/collection");
      } else {
        this.showModal("/collection");
      }
    },
    clickHistory() {
      // 添加点击历史按钮的方法
      this.setShow();

      this.$router.push("/history");
    },
    clickSupport() {
      // 添加点击反馈按钮的方法
      if (apiUtils.checkLogin()) {
        this.setShow();
        // 添加点击排行按钮的方法
        this.$router.push("/help");
      } else {
        this.showModal("/help");
      }
    },
    clickUserCenter() {
      // 添加点击个人中心按钮的方法

      if (apiUtils.checkLogin()) {
        this.setShow();
        this.$router.push("/userCenter");
      } else {
        this.showModal("/userCenter");
      }
    },
    setShow() {
      var newValue = !this.isShow;
      if (newValue) {
        this.display = "block";
      }
      setTimeout(() => {
        this.isShow = newValue;
        if (!newValue) {
          setTimeout(() => {
            this.display = "none";
          }, 300);
        }
      }, 10);
    },
    showModal(curent="") {
      this.isModalVisible = true;
      if(curent == "") {
         this.currentUrl = this.$rouoter.path;
      }
      this.currentUrl = curent;
    },
    onModalHide() {
      console.log("用户取消了操作");
      this.isModalVisible = false;
    },
    onModalOk( ) {
      console.log("用户确认了操作");
      this.isModalVisible = false;
      console.log(this.currentUrl);
      this.$router.push(this.currentUrl);
    },
  },
  watch: {
    //展示字段
    show(newValue) {
      if (newValue) {
        this.display = "block";
      }
      setTimeout(() => {
        this.isShow = newValue;
        if (!newValue) {
          setTimeout(() => {
            this.display = "none";
          }, 300);
        }
      }, 100);
    },
  },
};
</script>
