<!-- 收藏记录页 -->
<template>
  <!-- 加载遮罩 -->
  <LoadingCmp ref="loading" />
  <!-- 顶部 -->
  <TopCmp :topTitle="title" />

  <article class="main under-top-bar">
    <div class="mod-his">
      <!--头部tab切换部分 begin-->
      <section
        class="bookShelf-tab under-top-bar-fixed"
        v-if="this.dataList.length > 0"
      >
        <div class="tab-list">
          <!-- onEdit 开启编辑模式 -->
          <a
            id="tab_edit"
            :class="{ 'tab-edit': true, onEdit: onEdit }"
            @click="chooseEdit"
          ></a>
        </div>
      </section>
      <!-- 数据部分 -->
      <section class="his-normal" style="display: block">
        <ul class="his-list" id="his_list">
          <!-- choose 选择 unChoose 不选择  没有就不选择-->
          <li
            :class="['comic-item', item.choose]"
            v-for="item in dataList"
            :key="item.comicId"
          >
            <router-link :to="'/detail/' + item.comicId" class="comic-link">
              <div class="comic-cover">
                <img
                  class="cover-image"
                  :src="item.icon"
                  :alt="item.comicTitle"
                />
              </div>
              <div class="comic-info">
                <strong class="comic-title">{{ item.comicTitle }}</strong>
                <small class="comic-author">作者：{{ item.author }}</small>
                <small class="comic-update"
                  >更新到{{ item.updateChapter || "..." }}话</small
                >
                <div class="comic-continue">
                  <i class="continue-img"></i>
                  <span>收藏于{{ item.chapter }}话</span>
                </div>
              </div>
            </router-link>

            <div class="comic-choose" @click="selectOne(item)">
              <i class="icon-choose"></i>
              <i class="icon-unChoose"></i>
            </div>
          </li>
        </ul>
      </section>

      <!--编辑状态下的工具栏 begin-->
      <div
        class="mod-edit"
        id="his_edit"
        v-bind:style="{ display: onEdit ? '' : 'none' }"
      >
        <!-- 全选后 delAll -->
        <a
          :class="{ 'edit-choose': true, delAll: delAll }"
          id="his_edit_choose"
        >
          <i class="edit-choose-normal"></i>
          <i class="edit-choose-all"></i>
          <span v-if="delAll" @click="selectAll(false)">取消全选</span>
          <span v-if="!delAll" @click="selectAll(true)">全选</span>
        </a>
        <a class="edit-del" id="his_edit_del" @click="delBtn">
          <i class="edit-del-normal"></i>
          <i class="edit-del-empty"></i>
          <span>删除</span>
        </a>
      </div>
    </div>
  </article>

  <!-- 提示组件 -->
  <TipCmp ref="tip" @loadMore="handleScroll" />
  <!-- 返回顶部 -->
  <BackToTopCmp />
</template>
<script>
import TopCmp from "../common/TopCmp.vue";
import TipCmp from "../common/ListTipCmp.vue";
import BackToTopCmp from "../common/BackToTopCmp.vue";
import { decryptImageUrl } from "../utils/AES.vue";
import apiUtils from "../utils/ApiUtil.vue";
import LoadingCmp from "../common/LoadingCmp.vue";
export default {
  data() {
    return {
      title: "收藏 ",
      dataList: [],
      //编辑模式
      onEdit: false,
      //全选了
      delAll: false,
    };
  },
  methods: {
    //获取收藏数据
    async getCollectionData(page = 1, pageSize = 6) {
      //已经达到最大页了
      if (!this.$refs.tip.canScroll()) {
        return;
      }
      //遮罩
      this.$refs.loading.show();
      // //设置动效
      this.$refs.tip.loadingStart();

      try {
        var response = await apiUtils.getCollection({
          page: page,
          size: pageSize,
        });
        //解密图片
        await Promise.all(
          response.dataList.map(async (element) => {
            element.icon = await decryptImageUrl(element.icon);
          })
        );

        this.dataList = this.dataList.concat(response.dataList);

        this.currentPage = page;
        //数据总量
        var total = response.total;
        //最大页数
        var maxPage = Math.max(Math.ceil(total / pageSize), 1);
        //是否最大页
        this.$refs.tip.loadingEnd(
          this.currentPage == maxPage,
          this.dataList.length > 0
        );
        //遮罩
        this.$refs.loading.hide();
      } catch (error) {
        this.$refs.tip.hasError();
      }
    },
    handleScroll() {
      this.getCollectionData(this.currentPage + 1);
    },
    chooseEdit() {
      //进入或者退出编辑模式
      this.onEdit = !this.onEdit;
      //是否全选
      this.delAll = this.queryDelAll();
      //进入编辑模式
      this.enterEditMode();
    },
    queryDelAll() {
      //数据列表是否全选了
      for (let index = 0; index < this.dataList.length; index++) {
        const element = this.dataList[index];
        if (element.choose != "choose") {
          return false;
        }
      }
      return true;
    },
    enterEditMode() {
      //进入编辑模式
      for (let index = 0; index < this.dataList.length; index++) {
        const element = this.dataList[index];
        element.choose = this.onEdit ? "unChoose" : "";
      }
    },
    selectAll(selected) {
      //全选
      for (let index = 0; index < this.dataList.length; index++) {
        const element = this.dataList[index];
        element.choose = selected ? "choose" : "unChoose";
      }
      this.delAll = selected;
    },
    selectOne(element) {
      //选择或者取消选择一条数据
      element.choose = element.choose == "choose" ? "unChoose" : "choose";
      this.delAll = this.queryDelAll();
    },
    delBtn() {
      //删除选中的数据
      var delList = [];
      for (let index = 0; index < this.dataList.length; index++) {
        const element = this.dataList[index];
        if (element.choose == "choose") {
          delList.push(element.comicId);
        }
      }

      apiUtils.delCollection({ ids: delList }).then(() => {
        this.chooseEdit();
        this.dataList = [];
        this.$refs.tip.reset();
        this.getCollectionData(1);
      });
    },
  },
  components: {
    TopCmp,
    TipCmp,
    BackToTopCmp,
    LoadingCmp,
  },
  mounted() {
    this.$setMetaData("收藏");
    this.getCollectionData(1);
  },
};
</script>
<style scoped>
.main .mod-his .his-normalfuck {
  background-color: #f5f5ee;

  padding-top: 1px;
  padding-bottom: 1px;
}

.main .mod-his .his-normal .his-list .comic-continue {
  position: absolute;
  z-index: 1;
  right: 3rem;
  top: 1.5rem;
  width: 3.25rem;
}

