    };
  },
  components: {
    TopCmp,
   // RecommendCategoryCmp,
    LoadingCmp,
    CategoryCmp,
   
  },
  methods: {
    onChildLoaded() {
      this.loadedCount++;
      if (this.loadedCount >= 1) {
       
