import { createApp } from 'vue'
import { createRouter, createWebHashHistory } from 'vue-router';
import App from './App.vue';
import './assets/css/core.css';
import home from './components/HomePage.vue';
import searchPage from './components/search/SearchPage.vue'
import resultPage from './components/search/SearchResultPage.vue'
import categoryPage from './components/categories/CategoryPage.vue'
import comicList from './components/dataList/ComicListPage.vue'
import rankPage from './components/rank/RankPage.vue'
import historyPage from './components/history/HistoryPage.vue'
import ModalConfirm from './components/common/ConfirmCmp.vue';
import collectionPage from './components/history/CollectionPage.vue';
import helpPage from './components/help/HelpPage.vue'
import helpDetail from './components/help/HelpDetailPage.vue'
import register from './components/users/RegistePage.vue'
import registerStep2 from './components/users/RegisteStep2Page.vue'
import login from './components/users/LoginPage.vue'
import UserCenter from './components/users/UserCenter.vue'
import buyList from './components/history/BuyListPage.vue'
import pay from './components/pay/PayPage.vue'
import payList from './components/history/PayListPage.vue'
import detail from './components/comic/ComicDetailPage.vue'
import read from './components/comic/ReadPage.vue'

import DocUtil from './components/utils/DocUtil.vue'

import err from './components/common/ErrPage.vue'

// 路由表
const routes = [
    {
        path: '/',
        name: 'home',
        component: home,


    }
    , {
        path: "/search",
        component: searchPage,
        name: '搜索页面'
    }
    , {
        path: "/searchResult/:key",
        component: resultPage,
        name: '搜索结果页面'
    }
    , {
        path: "/category",
        component: categoryPage,
        name: '分类菜单页面'
    }
    , {
        path: "/comicList/:categoryName/:categoryId",
        component: comicList,
        name: '分类列表页面'
    }
    , {
        path: "/rank",
        component: rankPage,
        name: '排行榜页面'
    }
    , {
        path: "/history",
        component: historyPage,
        name: '历史记录页面'
    }
    , {
        path: "/collection",
        component: collectionPage,
        name: '收藏页'
    }
    , {
        path: "/help",
        component: helpPage,
        name: '帮助页'
    }, {
        path: "/helpDetail/:qid",
        component: helpDetail,
        name: '帮助详情页'
    }, {
        path: "/registe",
        component: register,
        name: '注册页面'
    }, {
        path: "/registeStep2/:phone",
        component: registerStep2,
        name: '注册页面 第二步'
    }, {
        path: "/login",
        component: login,
        name: '登陆页面'
    }, {
        path: "/userCenter",
        component: UserCenter,
        name: '用户中心'
    }, {
        path: "/buyList",
        component: buyList,
        name: '购买记录'
    }, {
        path: "/pay",
        component: pay,
        name: '充值页面'
    }, {
        path: "/payList",
        component: payList,
        name: '充值记录页面'
    }, {
        path: "/detail/:id",
        component: detail,
        name: '漫画详情页'
    }, {
        path: "/read/:id/:seq",
        component: read,
        name: '漫画阅读页面'
    }, {
        path: "/err/:id/:seq",
        component: err,
        name: '内容不存在页面'
    }
];
// 创建路由对象
const router = createRouter({
    history: createWebHashHistory(),
    routes,
    base: process.env.BASE_URL,
})
//创建app
const app = createApp(App)
app.use(router)
app.use(DocUtil)
app.component("modal-confirm", ModalConfirm)
app.mixin
app.mount('#app')
