<!-- 用户中心 -->
<template>
  <!-- 顶部 -->
  <TopCmp :topTitle="title" />

  <article class="main under-top-bar homebg">
    <section class="mod-user-info">
      <div class="user-info">
        <div class="user-head"><img :src="userIcon" alt="" /></div>
        <div class="user-detail">
          <p class="user-name">{{ userName }}</p>
          <p class="user-balance">
            剩余点券：<span id="text_balance_count">{{ userCoin }}</span
            ><span>点券</span>
          </p>
        </div>
      </div>
    </section>

    <section class="mod-item mod-history">
      <a data-name="history" @click="handleClick('history')">
        <div class="mod-title">阅读历史</div>
        <div class="sub-info"><i class="icon-arrow-right"></i></div>
      </a>
    </section>

    <section class="mod-item mod-bought">
      <a @click="handleClick('buy_list')">
        <div class="mod-title">已购章节</div>
        <div class="sub-info"><i class="icon-arrow-right"></i></div>
      </a>
    </section>

    <section class="mod-item mod-collect">
      <a data-name="collect" @click="handleClick('collect')">
        <div class="mod-title">我的收藏</div>
        <div class="sub-info"><i class="icon-arrow-right"></i></div>
      </a>
    </section>

    <section class="mod-item mod-recharge">
      <a data-name="recharge" @click="handleClick('recharge')">
        <div class="mod-title">点券充值</div>
        <div class="sub-info"><i class="icon-arrow-right"></i></div>
      </a>
    </section>

    <section class="mod-item mod-recharge">
      <a data-name="recharge_record" @click="handleClick('recharge_record')">
        <div class="mod-title">充值记录</div>
        <div class="sub-info"><i class="icon-arrow-right"></i></div>
      </a>
    </section>
    <!-- set password -->
    <section class="mod-item mod-recharge">
      <a data-name="set_password" @click="handleClick('set_password')">
        <div class="mod-title">设置密码</div>
        <div class="sub-info"><i class="icon-arrow-right"></i></div>
      </a>
    </section>

    <section class="mod-item mod-feedback">
      <a id="btn_support" @click="handleClick('support')">
        <div class="mod-title">意见反馈</div>
        <div class="sub-info"><i class="icon-arrow-right"></i></div>
      </a>
    </section>

    <section class="mod-item mod-logout">
      <a id="btn_login_out" @click="handleClick('login_out')"> 退出登录 </a>
    </section>

    <section class="icon-home-fixed">
      <a class="icon-home" href="/">[回到书库]</a>
    </section>
  </article>
</template>
<script>
import TopCmp from "../common/TopCmp.vue";
import apiUtils from "../utils/ApiUtil.vue";
import { decryptImageUrl } from "../utils/AES.vue";
export default {
  data() {
    return {
      title: "个人中心",
      userIcon: "",
      userName: "",
      userCoin: 0,
    };
  },
  methods: {
    handleClick(action) {
      switch (action) {
        case "history":
          //阅读历史
          this.$router.push("/history");
          break;
        case "buy_list":
          // Handle click on '已购章节'
          this.$router.push("/buyList");
          break;
        case "collect":
          // Handle click on '我的收藏'
          this.$router.push("/collection");
          break;
        case "recharge":
          // Handle click on '点券充值'
          this.$router.push("/pay");
          break;
        case "recharge_record":
          // Handle click on '充值记录'
          this.$router.push("/payList");
          break;
        case "support":
          // Handle click on '意见反馈'
          this.$router.push("/help");
          break;
        case "set_password":
          this.$router.push("/registeStep2/1");
          break;
        case "login_out":
          // Handle click on '退出登录'
          apiUtils.logout();
          this.$router.push("/");
          break;
        default:
          console.warn(
            `'Unknown action ${action} passed to handleClick method'`
          );
      }
    },
  },
  components: {
    TopCmp,
  },
  mounted() {
    this.$setMetaData("个人中心");
    apiUtils.getUserInfo().then(async (data) => {
      try {
        this.userIcon = await decryptImageUrl(data.userIcon);
      } catch (error) {
        console.log(error);
      }

      this.userName = data.userName;
      this.userCoin = data.userCoin;
    });
  },
  unmounted() {},
};
</script>
