<!-- 热门6图 推荐模块推荐 -->
<template>

    <section class="hot-comic mod-item" v-if="show">
        <h2 class="sub-title">
            <strong class="title-content"> {{ titleContent }}</strong>
        </h2>

        <ul class="comic-list col-3 recommend-list">
            <li class="comic-item" v-for="(comic, index) in comics" :key="index">
                <a class="comic-link" href="javascript:;" :data-id="comic.id" :data-pos="index + 1"
                    @click="fuckClick(comic.id)">
                    <div class="comic-cover">
                        <img class="cover-image" :src="comic.cover">
                    </div>
                    <div class="comic-content">
                        <strong class="comic-title">{{ comic.title }}</strong>
                        <small class="comic-desc">{{ comic.desc }}</small>
                    </div>
                </a>
            </li>
        </ul>


        <div class="btn-replacement-data" @click="refreshData">[换一批]</div>
    </section>
</template>

<script >
import recommendUtils from '../utils/Recommend.vue';
export default {
    data() {
        return {
            titleContent: "",
            comics: [],
            show: false
        }
    },
    mounted() {
        this.fetchData();
    },
    methods: {
        fetchData() {
            recommendUtils.getSearch42().then(data=>{
                this.titleContent = data.title;
                this.comics = data.list;
                this.show = true;
            })
           
        },
        refreshData() {
            this.fetchData();
        }, fuckClick(id) {
            this.$router.push("/detail/" + id);
        }
    }
};
</script>
