<template>
  <header :class="{ 'top-bar': true, 'no-title': false }">
    <a class="btn-top back" @click="back">返回</a>
    <h1 class="top-title">{{ title }}</h1>
    <div class="padding"></div>
  </header>

  <article class="main under-top-bar">
    <div class="mod-his">
      <!-- 数据部分 -->
      <section class="his-normal" style="display: block">
        <ul class="his-list" id="his_list" style="padding-top: 10px">
          <!-- choose 选择 unChoose 不选择  没有就不选择-->
          <li class="comic-item">
            <a class="comic-link">
              <div class="comic-info content-fix">
               <!-- 文字太长，自动换行 -->
                <b style="font-size: 0.8rem;color: #535252; padding:25px; letter-spacing: 1px; line-height: 1.5; "   >{{ answer }}</b>
                <!-- 遍历imgs，获得图片src 显示图片列表  -->
              </div>
            
            </a>
              <div >
                <img v-for="img in imgs" :src="img" :key="img" />
              </div>
          </li>
        </ul>
      </section>
    </div>

    <div class="faq-vote">
      <span>
        <div class="vote-icon">
          <img
            @click="feedback(true)"
            src="data:image/png;base64,"
            alt="good"
          />
        </div>
        <div v-if="feedBacks">很棒，解决了我的问题</div>
        <div v-if="good">感谢您的反馈</div>
      </span>
      <span>
        <div class="vote-icon">
          <img
            @click="feedback(false)"
            src="data:image/png;base64,"
            alt="bad"
          />
        </div>
        <div v-if="feedBacks">不是我想要的答案</div>
        <div v-if="bad">感谢您的反馈</div>
      </span>
    </div>

    <div class="mint-cell-value"><span></span></div>

    <section class="faq-remark">
      <span
        >仍无法解决您的问题？点击<a @click="submit" class="">提交反馈</a></span
      >
    </section>
  </article>

  <!-- 提示框 -->
  <modal-confirm
    v-if="isModalVisible"
    message="联系qq管理员解决您的问题"
    cancel-text="取消"
    ok-text="打开qq"
    @hide="onModalHide"
    @ok="onModalOk"
  ></modal-confirm>
</template>
<script>
import apiUtils from "../utils/ApiUtil.vue";
import {decryptImageUrl} from "../utils/AES.vue";
export default {
  data() {
    return {
      title: "帮助与反馈",
      questionTitle: " ",
      answer: "",
      imgs: [],
      isModalVisible: false,
      feedBacks: true,
      good: false,
      bad: false,
    };
  },
  mounted() {
    this.$setMetaData("帮助与反馈");
    this.getData();
  },
  methods: {
    async getData() {
      var qid = this.$route.params.qid;

      var data = await apiUtils.getHelpDetail({ id: qid });
      //获取远程数据。包括是否反馈、反馈结果等
      this.title = data.title;
      this.answer = data.content;
      this.imgs = data.imgList;
      await Promise.all(
        this.imgs.map(async (img, index) => {
          this.imgs[index] = await decryptImageUrl(img);
        })
      );
      this.feedBacks = data.feedback == 0;
      this.good = data.feedback == 1;
      this.bad = data.feedback == 2;
    },
    back() {
      this.$router.back();
    },
    submit() {
      this.showModal();
    },
    showModal() {
      this.isModalVisible = true;
    },
    onModalHide() {
      console.log("用户取消了操作");
      this.isModalVisible = false;
    },
    onModalOk() {
      console.log("用户确认了操作");
      this.isModalVisible = false;
    },
    feedback(f) {
      if (!this.feedBacks) {
        return;
      }
      var qid = this.$route.params.qid;
      apiUtils.feedBack({ id: qid, feedback: f ? 1 : 2 });
      this.feedBacks = false;
      this.bad = !f;
      this.good = f;
    },
  },
};
</script>

