<script>
import axios from "axios";
import historyUtils from "./History.vue";
import { decryptData, decryptFixedData } from "../utils/AES.vue";
// const baseUrl = "http://localhost:3561/service/";
const  baseUrl = "https://service.swinner.fun/service/";
// 创建一个 axios 实例 用于鉴权
const api = axios.create({
  baseURL: baseUrl,
  timeout: 50000,
  withCredentials: true, // 允许发送跨域 Cookie
});

// 创建一个 axios 实例 无需鉴权
const noAuthApi = axios.create({
  baseURL: baseUrl,
  timeout: 50000,
  withCredentials: true, // 允许发送跨域 Cookie
});

// 添加请求拦截器
api.interceptors.request.use((config) => {
  // 在发送请求前检查用户是否已经登录，可以从 cookie 或 localStorage 中获取登录状态
  const isLoggedIn = checkUserLoginStatus(); // 自定义函数，用于检查用户登录状态
  if (isLoggedIn) {
    // 如果用户已经登录，则添加 token 到请求头中
    const token = getToken(); // 自定义函数，用于获取用户登录后的 token
    config.headers.token = `${token}`;
  } else {
    // 如果用户未登录，则重定向到登录页面
    redirectToLoginPage(); // 自定义函数，用于跳转到登录页面
  }
  return config;
});

api.interceptors.response.use(
  async (response) => {
    // 处理响应数据
    var data = response.data;
    if (response.config.url.indexOf("sysConfig.aspx") == -1) {
      data = await decryptData(data);
    } else {
       data.data = await decryptFixedData(data.data);
    }

    if (data && data.code === 200) {
      return data.data;
    } else if (data.code == 404) {
      const req = JSON.parse(response.config.data);
      const bookId = req["comicId"];
      const seq = req["seq"];
      return (window.location.href = "/#/err/" + bookId + "/" + seq); // 错误页面
    } else {
      console.error("Response error:", data);
      return Promise.reject(new Error(data.message));
    }
  },
  (error) => {
    // 处理响应错误
    console.error("Response error:", error);
    return Promise.reject(error);
  }
);

noAuthApi.interceptors.response.use(
  async (response) => {
    // 处理响应数据
    var data = response.data;
    if (response.config.url.indexOf("sysConfig.aspx") == -1) {
      data = await decryptData(data);
    } else {
      data.data = await decryptFixedData(data.data);
    }
    if (data && data.code === 200) {
      return data.data;
    } else if (data.code == 404) {
      console.log(response.config.url);
      const url = new URL(baseUrl + "/" + response.config.url);
      const bookId = url.searchParams.get("bookId");
      const seq = url.searchParams.get("seq");
      return (window.location.href = "/#/err/" + bookId + "/" + seq); // 重定向到错误
    } else {
      console.error("Response error:", data);
      return Promise.reject(new Error(data.msg));
    }
  },
  (error) => {
    // 处理响应错误
    console.error("Response error:", error);
    return Promise.reject(error);
  }
);

function checkUserLoginStatus() {
  const isLoggedIn = window.localStorage.getItem("isLoggedIn"); // 从 localStorage 中获取登录状态

  return isLoggedIn === "true";
}

function getToken() {
  const token = localStorage.getItem("token"); // 尝试从本地存储中获取 token
  if (token) {
    return token; // 如果找到 token，则返回它
  } else {
    return null; // 如果找不到 token，则返回 null
  }
}

function redirectToLoginPage() {
  window.location.href = "/#/login"; // 重定向到登录页
}

//不带缓存的api()
function noCacheApi(axObj, api, options) {
  return axObj.get(api, options).then((response) => {
    return Promise.resolve(response);
  });
}

// 带有缓存的api
function cacheApi(axObj, api, options, cacheTime = 1000*60*10 ) {
  //const cacheTime = 1000 * 60 * 24;//缓存毫秒

  const cacheKey = `${api}_${JSON.stringify(options)}`;
  const cachedData = localStorage.getItem(cacheKey);
  if (cachedData) {
    const { createTime, response } = JSON.parse(cachedData);
    const now = new Date().getTime();
    if (now - createTime <= cacheTime) {
      return Promise.resolve(response);
    }
  }
  return axObj.get(api, options).then((response) => {
    const createTime = new Date().getTime();
    const cacheData = JSON.stringify({ createTime, response });
    localStorage.setItem(cacheKey, cacheData);
    return Promise.resolve(response);
  });
}

// 封装各种后端 API 接口的调用方法
const apiUtils = {
  //获取用户信息
  getUserInfo: () => api.post("/user.php"),
  //指定的推荐信息
  getRecommend: (type) =>
    cacheApi(noAuthApi, "/recommend/info.aspx?type=" + type),
  //获取seo相关信息
  getSeo: () => cacheApi(noAuthApi, "/seo.php"),
  //获取ad配置
  getAd: () => cacheApi(noAuthApi, "/ad/index.aspx"),
  //3:1 4图推荐
  getHot4: () => noCacheApi(noAuthApi, "/assets/hotComic3.json"),
  //2:2 4图推荐
  getHot4_2: () => noCacheApi(noAuthApi, "/assets/hotComic2.json"),
  //获取6图热门推荐
  get6Hot: () => noCacheApi(noAuthApi, "/assets/hotComic.json"),
  //详情页的推荐数据
  get6HotForDetail: () =>
    noCacheApi(noAuthApi, "/assets/hotForDetailComic.json"),
  //查询分类菜单
  getCat: () => cacheApi(noAuthApi, "/category/list.aspx"),
  //获取推荐分类
  getRecommendCat: () =>
    cacheApi(noAuthApi, "/assets/recommendCategoryData.json"),
  //查询漫画列表
  getCommicList: (data) =>
    noCacheApi(
      noAuthApi,
      `/category/comic.aspx?p=${data.page}&s=${data.pageSize}&o=${data.orderBy}&d=${data.id}`
    ),
  //查询排行榜
  getRank: (data) =>
    cacheApi(noAuthApi, `/rank/list.aspx?p=${data.p}&s=${data.s}&o=${data.o}`),
  //查询收藏列表
  getCollection: (data) => api.post("/collection/list.php", data),

  //删除收藏
  delCollection: (data) => api.post("/collection/delete.php", data),
  //添加收藏
  addCollection: (data) => api.post("/collection/add.php", data),
  //同步历史记录
  getHistory: () => api.post("/history/pull.php"),
  //提交历史记录 修改为post
  pushHistory: (data) => api.post("/history/push.php", { data: data }),
  //删除历史记录
  deleteHistory: (data) => api.post("/history/delete.php", { data: data }),
  //没有内容时的推荐
  getRecommendHotSearch: () =>
    cacheApi(noAuthApi, "/assets/hotSearchDataWithNoResult.json"),
  //智能搜索
  smartQuery: (data) =>
    cacheApi(noAuthApi, `/assets/smartSearch.json?query=${data.key}`),
  //推荐搜索
  getHotQuery: () => cacheApi(noAuthApi, `/recommend/hotSearch.aspx`),
  //执行搜索
  query: (data) =>
    cacheApi(
      noAuthApi,
      `/search/list.aspx?p=${data.p}&s=${data.s}&k=${data.k}`
    ),
  //帮助列表
  getHelp: (data) => api.post("/help/list.aspx", data),

  //帮助详情
  getHelpDetail: (data) => api.post("/help/info.aspx", data),

  //帮助内容反馈
  feedBack: (data) => api.post("/help/feedback.aspx", data),

  //获取系统配置
  getSystemConfig: () => cacheApi(noAuthApi, "/sysConfig.aspx"),
  //登录相关。。。。。。。。。。。。。。。。。。。。。。。。。。。。。。。。。。。。。。。。。。。。。。。。
  //获取验证码
  getCode: (data) =>
    noCacheApi(noAuthApi, `/sms.php?phone=${data.phone}&type=${data.type}`),
  //登录 对接服务器后使用post
  login: (data) => noAuthApi.post("/login.php", { data: data }), //deng
  //注册
  register: (data) => noAuthApi.post(`/register.php `, { data: data }),
  //设置密码
  setPwd: (data) => api.post(`/setPwd.php`, { data: data }),
  //获取购买记录
  getBuyList: (data) => api.post(`/purchase/list.php`, data),
  //获取充值订单列表 充值记录
  getOrderList: (data) => api.post(`/order/list.aspx`, data),
  //创建订单
  createOrder: (data) => api.post(`/order/create.aspx`, data),
  //校验是否存在未支付的订单
  checkOrder: () => api.post(`/order/check.aspx`),
  //获取支付链接
  getPayOrderUrl: () => api.post(`/order/payOrderUrl.aspx`),
  //获取没有存在的订单详情
  getOrderInfo: () => api.post(`/order/info.aspx`),
  //  获取目录列表
  loadMoreChapter: (data) =>
    cacheApi(
      noAuthApi,
      `/comic/chapterList.php?id=${data.comicId}&page=${data.page}&sort=${data.sortAsc}`
    ),
  //获取阅读页的章节列表
  loadReadChapters: (data) =>
    cacheApi(
      noAuthApi,
      `/read/chapterList.php?d=${data.d}&p=${data.p}&o=${data.o}`
    ),
  //不登陆获取章节列表
  loadReadChaptersWithLogin: (data) =>
    cacheApi(
      api,
      `/assets/chapterOnReadPage.json?page=${data.page}&pageSize=${data.pageSize}&asc=${data.sortAsc}`
    ),
  //获取章节购买列表
  loadChapterBuyList: (data) =>
    api.get(`/comic/chapterBuyList.php?id=${data.comicId}`),
  //获取漫画的收藏状态
  getCollectionStatus: (data) =>
    api.post(`/collection/status.php`, { id: data.comicId }),
  //获取漫画信息
  getComicInfo: (data) =>
    cacheApi(
      noAuthApi,
      `/comic/info.php?id=${data.comicId}&page=${data.page}&s=10`
    ),
  //获取漫画阅读页的信息
  loadComic: (data) => cacheApi(noAuthApi, `/read/info.php?id=${data.comicId}`),
  //加载图片内容
  loadComicContent: (data) => noAuthApi.post(`/read/content.php`, data),

  //购买章节
  buy: (data) => api.post(`/purchase/buy.php`, data),
  //校验是否登陆
  checkLogin: () => checkUserLoginStatus(),
  //获取用户token
  token: () => getToken(),

  //登陆
  setLogin: (token) => {
    localStorage.setItem("token", token);
    window.localStorage.setItem("isLoggedIn", true);
    //登录的时候同步历史记录
    //  historyUtil.syncHistory();
    historyUtils.mergeReadingHistoryOnLogin();
  },
  //登出
  logout: () => {
    localStorage.setItem("token", "");
    window.localStorage.setItem("isLoggedIn", false);
  },
};
export default apiUtils;
</script>
