<!-- 注册页面第二步-->
<template>
  <div id="__layout">
    <div id="app">
      <div class="login">
        <div class="header">
          <div class="back" @click="back"></div>
          <!-- 页面标题设置密码 -->
        <div class="title">设置密码</div>
        </div>
        <div class="content">
          <div class="input code">
            <!-- 输入框 -->
            <input
              @input="change"
              :type="showPwd ? 'text' : 'password'"
              placeholder="输入密码"
              v-model="pwd"
            />
          </div>

          <div class="input code">
            <!-- 输入框 -->
            <input
              @input="change"
              :type="showPwd ? 'text' : 'password'"
              placeholder="确认密码"
              v-model="pwd2"
            />
          </div>

          <!-- .user-info-success  可用 否则不可用 -->
          <div
            class="btn"
            :class="{ 'user-info-success': canSubmit }"
            @click="login"
          >
            完成
          </div>
        </div>
        <!-- 错误提示 -->
        <div :class="{ 'message-pop': true, hide: !isShow }" v-show="isShow">
          <div class="content">
            {{ message }}
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import apiUtils from "../utils/ApiUtil.vue";
export default {
  data() {
    return {
      phoneNumber: "",
      code: "",
      timer: null,

      message: "",
      canSubmit: false,
      usePwd: false,
      checked: false,
      showPwd: false,
    };
  },
  methods: {
    getCode() {
      const reg = /^[1][3,4,5,6,7,8,9][0-9]{9}$/;
      if (reg.test(this.phoneNumber)) {
        if (this.countDown === 60) {
          // mock API call to get code
          console.log("get code API call");
          this.startCountDown();
        } else {
          console.log("fuck man");
        }
      } else {
        console.error("请输入正确的手机号码");
        this.showErrorTip("请输入正确的手机号码");
      }
    },
    startCountDown() {
      this.timer = setInterval(() => {
        this.countDown--;
        if (this.countDown === 0) {
          clearInterval(this.timer);
          this.countDown = 60;
        }
      }, 1000);
    },
    login() {
      if (!this.canSubmit) {
        return;
      }
      this.canSubmit = false;
      apiUtils.setPwd({ pwd: this.pwd }).then(() => {
        //如果返回成功，那么跳转到用户中心
        this.$router.push("/userCenter");
      });
    },
    showErrorTip(message) {
      //错误提示
      this.isShow = true;
      this.message = message;
      setTimeout(() => {
        this.hideErrorTip();
      }, 3000);
    },
    hideErrorTip() {
      //隐藏错误信息
      this.isShow = false;
      this.message = "";
    },
    change() {
      this.canSubmit = this.pwd == this.pwd2 && this.pwd.length > 5;
    },
    changeLoginType() {
      //切换登录方式：使用密码或者手机验证码
      this.usePwd = !this.usePwd;
    },
    back() {
      this.$router.back();
    },
    checkProtocol() {
      this.checked = !this.checked;
    },
    toggleShowPwd() {
      this.showPwd = !this.showPwd;
    },
  },
  mounted() {
    this.$setMetaData("设置密码");
  },
};
</script>

