<!-- 帮助列表页 -->
<template>
  <!-- 顶部 -->
  <TopCmp :topTitle="title" />

  <article class="main under-top-bar">
    <div class="mod-his">
      <!-- 数据部分 -->
      <section class="his-normal" style="display: block">
        <ul class="his-list" id="his_list" style="padding-top: 10px">
          <!-- choose 选择 unChoose 不选择  没有就不选择-->
          <li
            :class="['comic-item', item.choose]"
            v-for="item in helpList"
            :key="item.id"
          >
            <router-link :to="'/helpDetail/' + item.id" class="comic-link">
              <div class="comic-info">
                <strong>{{ item.title }}</strong>
                <small class="comic-author"
                  >更新日期：{{ item.updateAt }}</small
                >
              </div>
            </router-link>
          </li>
        </ul>
      </section>
    </div>
  </article>

  <!-- 提示组件 -->
  <TipCmp ref="tip" @loadMore="handleScroll" />
  <!-- 返回顶部 -->
  <BackToTopCmp />
</template>
<script>
import TopCmp from "../common/TopCmp.vue";
import TipCmp from "../common/ListTipCmp.vue";
import BackToTopCmp from "../common/BackToTopCmp.vue";
import apiUtils from "../utils/ApiUtil.vue";
export default {
  data() {
    return {
      title: "帮助与反馈",
      helpList: [],
    };
  },
  methods: {
    async getComicData(page = 1, pageSize = 10) {
      //已经达到最大页了
      if (!this.$refs.tip.canScroll()) {
        return;
      }
      // //设置动效
      this.$refs.tip.loadingStart();

      var data = await apiUtils.getHelp({ page: page, size: pageSize });
      try {
        this.helpList = this.helpList.concat(data.dataList);
        this.currentPage = page;
        const total = data.total;
        var maxPage = Math.ceil(total / pageSize);
        //是否最大页
        this.$refs.tip.loadingEnd(
          this.currentPage == maxPage,
          this.helpList.length > 0
        );
      } catch (error) {
        this.$refs.tip.hasError();
      }
    },
    handleScroll() {
      this.getComicData(this.currentPage + 1);
    },
  },
  components: {
    TopCmp,
    TipCmp,
    BackToTopCmp,
  },
  mounted() {
    this.$setMetaData("帮助与反馈");
    this.getComicData(1);
   // window.addEventListener("scroll", this.handleScroll);
  },
  unmounted() {
  //  window.removeEventListener("scroll", this.handleScroll);
  },
};
</script>
<style scoped>
.main .mod-his .his-normalfuck {
  background-color: #f5f5ee;

  padding-top: 1px;
  padding-bottom: 1px;
}
</style>
