<!-- 漫画图片组件 -->
<template>
  <li
    :class="[
      'comic-pic-item',
      { 'pic-unloaded': !loaded },
      { 'pic-loaded': loaded },
    ]"
    :index="index"
    :seq="seq"
    :total="total"
  >
    <div class="comic-pic-pad" :style="{ paddingTop: padTop }">
      <div class="err-info" v-if="loadError">
        <span>嗷呜~图片加载中请稍等</span>
        <a class="reload-btn" @click="reloadImage">重新加载</a>
      </div>
    </div>
    <div class="comic-pic-box">
      <img
        class="comic-pic"
        :key="imageKey"
        :src="realImg"
        @load="onImageLoad"
        @error="onImageError"
        :data-timeout="imageTimeout"
        ref="image"
      />
    </div>
  </li>
</template>
<script>
import { decryptImageUrl } from "../utils/AES.vue";
export default {
  props: {
    //当前正在读的页数
    currentIndex: {
      type: Number,
      required: true,
    },
    //第几页
    index: {
      type: Number,
      required: true,
    },
    //第几章
    seq: {
      type: String,
      required: true,
    },
    timeout: {
      type: Number,
      default: 0,
    },
    imageUrl: {
      type: String,
      required: true,
    },
    scrollToMe: {
      type: Boolean,
      default: false,
    },
    total: {
      type: Number,
      default: 0,
    },
  },
  data() {
    return {
      padTop: "110%",
      imageTimeout: 2000,
      loadError: false,
      loaded: false,
      imageKey: 0,
      realImg: "",
    };
  },
  async mounted() {
    // 解密图片地址
    // this.realImg = await decryptImageUrl(this.imageUrl);
   // this.onCurrentIndexChange();
  },
  methods: {
    onImageLoad() {
      if (this.$refs.image) {
        this.padTop = `${
          (this.$refs.image.clientHeight / this.$refs.image.clientWidth) * 100
        }%`;
        this.loaded = true;
        this.imageKey = Date.now();
        //emit event
        this.$emit("loaded", this.index);
      } else {
        // handle the case where this.$refs.image is null
        console.warn("Image element not found");
      }
    },

    onImageError() {
      this.loadError = true;
    },
    async reloadImage() {
      this.loadError = false;
      // 图片加载错误时，将key的值加1，触发重新加载
      this.realImg = await decryptImageUrl(this.imageUrl);
      this.imageKey += 1;
    },
    //当currentIndex 变化
    onCurrentIndexChange() {
      var curr = Number(this.currentIndex);

      //如果页面在当前页面的5个页内，那么加载自己
      if (curr + 5 >= this.index && !this.loaded &&
        curr - 5 <= this.index) {
        this.reloadImage();
      }
    },
  },

  //监控currentIndex的变化
  watch: {
    currentIndex: {
      handler: "onCurrentIndexChange",
      immediate: true,
    },
  },
};
</script>
