<!-- 数据列表提示 。加载中 、出错了、到底了 没有数据 -->
<template>
    <section class="result-box under-top-bar">
        <div class="not-found-info">
        <!-- 未找到内容 -->
        <div class="not-found-info" v-if="!hasData">
            <div class="npc-box">
                <i class="npc-not-found"></i>
                <p class="text-not-found">Sorry，这里还没有内容</p>
                <a class="go-to-index" href="/">好康漫画网</a>
            </div>
        </div>
         <!-- 未找到内容时的推荐组件 -->
     <recoment v-if="!hasData && cmp=='default'" />
     <comicList v-if="!hasData && cmp=='comicList'" />

    </div>
    
    </section>

    <section class="mod-load-more" v-if="hasData">
        <div class="mlm-status-loading" v-bind:style="{ 'display': loading ? 'block' : 'none' }">
            <div class="mlm-dots">
                <span class="mlm-dot"></span>
                <span class="mlm-dot"></span>
                <span class="mlm-dot"></span>
            </div>
            <p class="mlm-info">嘿咻嘿咻加载中</p>
        </div>
        <div class="mlm-status-over" v-bind:style="{ 'display': maxpage ? 'block' : 'none' }">
            大人，已经到底了哦~
        </div>
         <!-- 加载下一页按钮 -->
        <div class="mlm-status-again" v-bind:style="{ 'display': canScroll() ? 'block' : 'none' }">
            <a class="mlm-btn-again" @click="loadMore" style="padding:50px">
               点击加载更多
            </a>
        </div>
        <div class="mlm-status-again" v-bind:style="{ 'display': err ? 'block' : 'none' }">
            <a class="mlm-btn-again">
                再刷一次！&gt;_&lt;
            </a>
        </div>
    </section>

</template>
<script>
import recoment from '../search/HotSearchCmpWithNoResult.vue';
import comicList from  './HotComicOnListRecentCmp.vue'
export default {

    props: {
        //是否显示推荐组件
        cmp: {
            type: String,
            default: "default"
        }
    },
    data() {
        return {
            comicList: [],
            loading: false, maxpage: false, err: false,hasData:false,
            

        };
    }, components: { recoment,comicList  },
    methods: {
        //开始加载数据
        loadingStart(){
            this.loading = true;
            this.maxpage = false;
            this.hasData = true;
            this.err = false;
        },
        //结束加载数据
        loadingEnd(max,hasData){
            this.loading = false;
            this.maxpage = max;
            this.hasData = hasData;
            this.err = false;
        } 
         ,
         //发生错误
         hasError(){
            this.loading = false;
            this.maxpage = false;
            this.hasData = true;
            this.err = true;
         }
         ,
         canScroll(){
            return !this.loading &&  !this.maxpage
         }
         ,reset(){
            this.loading = false;
            this.maxpage = false;
            this.hasData = false;
            this.err = false;
         }
         ,loadMore(){
            this.$emit('loadMore');
         }
    }
}
</script>

