<template>
  <div>
    <div v-if="isCrawler">
      <p>抱歉，爬虫无法访问此内容。</p>
    </div>
    <div v-else-if="!isMobile">
      <section class="banner-menu under-top-bar">
        <!-- 添加样式标题展示，剧中展示 -->
        <div class="m-banner-wrap" style="width: 100%; height: 225px">
      
        <p>抱歉，此页面仅支持手机访问。</p>
        </div>
      </section>
    </div>
    <div v-if="valid">
      <!-- 在此处放置仅在手机端显示的内容 -->
      <p>欢迎来到我们的移动端页面！</p>
    </div>
  </div>
  <LoadingCmp ref="loading" v-if="valid" />
  <!-- 头部搜索区域 -->
  <TopCmp :topTitle="toptitle" />
  <!-- 加载中 -->

  <!-- Banner -->
  <keep-alive>
    <component
      :is="BannerCmp"
      :bannerData="bannerData"
      :interval="3000"
      v-if="bannerData.length > 0"
    />
  </keep-alive>
  <!-- app广告 -->
  <keep-alive>
    <component :is="AdCmp" />
  </keep-alive>
  <!-- 四图推荐1 -->
  <keep-alive>
    <component :is="HotComicFourBigCmp" />
  </keep-alive>
  <!-- 打滚推荐模块 -->
  <keep-alive>
    <component :is="HotComicFourCmp" />
  </keep-alive>
  <!-- 热门推荐模块 -->
  <keep-alive>
    <component :is="HotComicCmp" />
  </keep-alive>

  <!-- 返回顶部按钮 -->
  <BackToTopCmp />
  <!-- 底部区域 -->
  <footer class="bottom">
    <p>到底喽</p>
    <p>好康漫画网，海量漫画，高清画质一览无余</p>
    <p>2023给你好康</p>
    <p>@Copyright tiger</p>
  </footer>
</template>

<script>
import TopCmp from "./common/TopCmp.vue";
import BannerCmp from "./common/BannerCmp.vue";
import AdCmp from "./common/AdCmp.vue";
import HotComicCmp from "./common/HotComicSixCmp.vue";
import HotComicFourCmp from "./common/HotComicFourCmp.vue";
import HotComicFourBigCmp from "./common/HotComicFourBigCmp.vue";
import BackToTopCmp from "./common/BackToTopCmp.vue";
import recommendUtils from "./utils/Recommend.vue";
import LoadingCmp from "./common/LoadingCmp.vue";
export default {
  data() {
    return {
      activeIndex: 1,
      bannerData: [],
      interva: null,
      toptitle: "",
      BannerCmp: "BannerCmp",
      AdCmp: "AdCmp",
      HotComicFourBigCmp: "HotComicFourBigCmp",
      HotComicFourCmp: "HotComicFourCmp",
      HotComicCmp: "HotComicCmp",
    };
  },
  components: {
    TopCmp,
    BannerCmp,
    AdCmp,
    HotComicCmp,
    HotComicFourCmp,
    HotComicFourBigCmp,
    BackToTopCmp,
    LoadingCmp,
  },
  mounted() {
    this.$refs.loading.show();
    this.fetchData();
    //设置seo数据
    this.$setMetaData("首页");
  },
  methods: {
    fetchData() {
      recommendUtils
        .getBanner()
        .then((res) => {
          if (res) {
            this.bannerData = res.list;
          }
          this.$refs.loading.hide();
        })
        .catch((error) => {
          console.error(error);
        });
    },
  },
  computed: {
    isMobile() {
      const userAgent = navigator.userAgent;
      const mobileRegex =
        /Mobi|Android|iPhone|iPad|iPod|BlackBerry|Windows Phone/i;
      return mobileRegex.test(userAgent);
    },
    isCrawler() {
      const userAgent = navigator.userAgent;
      const crawlerRegex = /bot|googlebot|crawler|spider|robot|crawling/i;
      return crawlerRegex.test(userAgent);
    },
    valid() {
      return !this.isCrawler && this.isMobile;
    },
  },
};
</script>
